<template>
  <div class="page-home-aside">
    <div class="content">
      <dsf-menu ref="menu" :menu-list="menuList"></dsf-menu>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex';

export default {
  name: 'DsfHomeAside',
  inject: ['root'],
  provide() {
    return {
      aside: this
    };
  },
  data() {
    return {
      active: null,
      show: null
    }
  },
  computed: {
    ...mapState([
      'route'
    ]),
    menuList () {
      let ma = this.root.menuAction;
      ma = ma && ma.length ? ma[0] : {};
      if (ma && ma.children) {
        return ma.children;
      }
      return [];
    }
  },
  watch: {
    'route' () {
      this.$nextTick(() => {
        this.init();
      });
    }
  },
  mounted() {
    setTimeout(() => {
      this.init();
    }, 100);
  },
  methods: {
    init () {
      const menuIndex = this.root.menuIndex;
      let menu = this.$refs.menu;
      let active = null;
      for (let i = 1; i < menuIndex.length; i++) {
        let index = menuIndex[i];
        if (index < 0) break;
        menu = menu.$children[index];
        if (menu) {
          active = menu;
          menu = menu.$children;
          if (menu && menu.length) {
            menu = menu[0];
          } else {
            break;
          }
        } else {
          break;
        }
      }
      this.setActive(active, active.level);
      if (active.$parent && active.$parent.$parent &&
        active.$parent.$parent.$options._componentTag === 'dsf-menu-item') {
        this.setShow(active.$parent.$parent);
      }
    },
    setActive(active) {
      let parent = this.active;
      while (parent && parent.$options._componentTag === 'dsf-menu-item') {
        parent.isActive = false;
        parent = parent.$parent;
        if (parent) {
          parent = parent.$parent;
        } else {
          break;
        }
      }
      parent = active;
      while (parent && parent.$options._componentTag === 'dsf-menu-item') {
        parent.isActive = true;
        parent = parent.$parent;
        if (parent) {
          parent = parent.$parent;
        } else {
          break;
        }
      }
      this.active = active;
    },
    setShow(show) {
      let parent = this.show;
      while (parent && parent.$options._componentTag === 'dsf-menu-item') {
        parent.showChildren = false;
        parent = parent.$parent;
        if (parent) {
          parent = parent.$parent;
        } else {
          break;
        }
      }
      parent = show;
      while (parent && parent.$options._componentTag === 'dsf-menu-item') {
        parent.showChildren = true;
        parent = parent.$parent;
        if (parent) {
          parent = parent.$parent;
        } else {
          break;
        }
      }
      this.show = show;
    }
  }
}
</script>