<template>
  <DsfOneThingBox title="信息公开">
    <div
      slot="content"
      class="dsf-onething-banner"
      :class="[effect]"
      @mouseover="clearLoop"
      @mouseout="() => {loop && startLoop()}"
    >
      <transition-group v-if="effect !== 'slide'" name="opacity">
        <el-image
          v-for="(item, index) in list"
          v-show="item.id === activeId"
          :key="item.id+index"
          :src="item.img"
          :alt="item.text"
          :style="imgStyle"
          @click="link(item)"
        ></el-image>
      </transition-group>
      <div v-else :class="['slide-wrap', noTrans ? 'notrans' : '']" :style="wrapStyle">
        <el-image
          v-for="(item, index) in list"
          :key="item.id+index"
          :src="item.img"
          :alt="item.text"
          :style="imgStyle"
          @click="link(item)"
        ></el-image>
      </div>
      <div v-if="activeText" class="texts">
        <span
          v-for="(item, index) in newsList"
          :key="`text-${index}`"
          class="text-item"
          :title="item.text"
          @click="link(item)"
        >
          <i class="iconfont icon-liebiao1"></i>
          <span>{{ item.text }}</span>
        </span>
        <a class="more" href>更多<i class="iconfont icon-weibiaoti34"></i></a>
      </div>
      <ul v-if="page" class="page">
        <template v-for="(item, index) in list">
          <li
            v-if="index< 3"
            v-show="!item.vitual "
            :key="`item.page-${index}`"
            :class="{ active: item.id === activeId }"
            @click="toggle(item)"
          ></li>
        </template>
      </ul>
      <div v-if="btn" class="btn prev" @click="prev">
        <i class="iconfont icon-zuo"></i>
      </div>
      <div v-if="btn" class="btn next" @click="next">
        <i class="iconfont icon-zuo"></i>
      </div>
    </div>
  </DsfOneThingBox>
</template>
<script>
import control from "_platform/dsf/mixins/control";
import DsfOneThingBox from "./DsfOneThingBox";
export default dsf.component({
  ctrlCaption: "信息公开",
  mixins: [control],
  name: "DsfOneThingBanner",
  components: {
    DsfOneThingBox,
  },
  props: {
    dataSource: {
      type: Array,
      default() {
        return [{
            img: require('../../public/img/banner.png'),
            url: '',
            text: [{
              icon: 'icon-liebiao1',
              text: '解读居转户和人才引进落户最新政策'
            },{
              icon: 'icon-liebiao1',
              text: '解读居转户和人才引进落户最新政策'
            },{
              icon: 'icon-liebiao1',
              text: '解读居转户和人才引进落户最新政策'
            },{
              icon: 'icon-liebiao1',
              text: '解读居转户和人才引进落户最新政策'
            },{
              icon: 'icon-liebiao1',
              text: '解读居转户和人才引进落户最新政策'
            }]
        }]
      },
    },
    loop: {
      type: Boolean,
      default: false,
    },
    loopDelay: {
      type: Number,
      default: 3000,
    },
    page: {
      type: Boolean,
      default: true,
    },
    effect: {
      type: String,
      default: "opacity", // opacity,slide
    },
    btn: {
      type: Boolean,
      default: true,
    },
  },
  data() {
    return {
      list: [],
      newsList: [],
      activeId: "",
      width: 0,
      $timer: null,
      noTrans: false,
      isTransition: false,
    };
  },
  computed: {
    wrapStyle() {
      const index = this.list.findIndex((it) => it.id === this.activeId);
      return {
        width: `${this.width * this.list.length}px`,
        transform: `translateX(-${this.width * index}px)`,
      };
    },
    imgStyle() {
      const styles = {};
      if (this.effect === "slide") {
        styles.width = `${this.width}px`;
      }
      return styles;
    },
    activeText() {
      const target = this.list.find((it) => it.id === this.activeId);
      if (target && target.text) {
        return target.text;
      }
      return false;
    },
  },
  methods: {
    resize() {
      this.width = $(".dsf-onething-banner").width();
    },
    format(arr) {
      if (this.effect === "slide") {
        arr = [
          {
            ...arr[arr.length - 1],
             vitual: true,
          },
          ...arr,
          {
            ...arr[0],
             vitual: true,
          },
        ];
      }
      const list = arr.map((it, index) => {
        if (this.effect === "slide") {
          if (index === 1) {
            this.activeId = it.id;
          }
        } else {
          if (index === 0) {
            this.activeId = it.id;
          }
        }
        return {
          ...it,
        };
      });
      this.list = list;
    },
    prev() {
      if (!this.isTransition) {
        const index = this.list.findIndex((it) => it.id === this.activeId);
        let prevIndex = index - 1;
        if (this.effect === "slide") {
          if (prevIndex < 1) {
            this.isTransition = true;
            setTimeout(() => {
              this.noTrans = true;
              this.activeId = this.list[this.list.length - 2].id;
              this.isTransition = false;
              setTimeout(() => {
                this.noTrans = false;
              }, 0);
            }, 800);
          }
        } else {
          if (prevIndex < 0) {
            prevIndex = this.list.length - 1;
          }
        }
        this.activeId = this.list[prevIndex].id;
      }
    },
    next() {
      if (!this.isTransition) {
        const index = this.list.findIndex((it) => it.id === this.activeId);
        let nextIndex = index + 1;
        if (this.effect === "slide") {
          if (nextIndex > this.list.length - 2) {
            this.isTransition = true;
            setTimeout(() => {
              this.noTrans = true;
              this.activeId = this.list[1].id;
              this.isTransition = false;
              setTimeout(() => {
                this.noTrans = false;
              }, 0);
            }, 800);
          }
        } else {
          if (nextIndex > this.list.length - 1) {
            nextIndex = 0;
          }
        }
        this.activeId = this.list[nextIndex].id;
      }
    },
    link(item) {
      window.open('./page.html#/ot/otherpage/newsdetail/'+item.id, "_target");
    },
    toggle(item) {
      this.activeId = item.id;
    },
    clearLoop() {
      clearTimeout(this.$timer);
      this.$timer = null;
    },
    startLoop() {
      if (this.$timer) {
        this.clearLoop();
      }
      this.$timer = setTimeout(() => {
        this.next();
        this.startLoop();
      }, parseInt(this.loopDelay || 5000, 10));
    },
    getNoticeData() {
      this.$http.get('/ot/notice/getNoticeData')
        .then(({ data }) => {
          let dataList = _.map(data.data, (el) => {
            let img = _.isArray(JSON.parse(el.y_tzgg_004)) ? JSON.parse(el.y_tzgg_004)[0].absolutePath : ''
            return {
              id: el.ot_t_01_id,
              text: el.y_tzgg_001,
              img: img
            }
          })
          this.list = _.slice(dataList, 0 , 3)
          this.newsList = _.slice(dataList, 3,9)
          this.format(this.list);
        })
        .catch((ex) => {
          dsf.layer.message("加载出错", false);
        })
        .finally(() => {
          // this.loading = false;
        })
    }
  },
  mounted() {
    this.width = $(".dsf-onething-banner").width();
    window.addEventListener("resize", this.resize);
  },
  created() {
    this.getNoticeData()
  },
  destroyed() {
    window.removeEventListener("resize", this.resize);
    this.clearLoop();
  },
});
</script>