<template>
  <div class="ds-control ds-button-bar" :style="{ 'text-align': align }" style="vertical-align: middle;">
    <span>
      <template v-for="(button,$index) in showButtons">
        <DsfButton
          v-if="showAndHide(button)"
          :key="$index"
          :icon="getIcon(button)"
          :title="button.title"
          :disabled="button.disabled"
          :size="size"
          :type="button.type"
          :btn-style="button.btnStyle"
          @click="clickHandler(button,$event)"
        >{{ button.text }}</DsfButton>
      </template>
      <el-popover v-if="moreButtonsReal.length" :popper-class="'ds-morebutton-popover'" width="auto" trigger="click" class="ds-button__more" @show="popoverShow" @hide="popoverClose">
        <template v-slot:default>
          <!-- <table>
            <tr v-for="(row,$index) in moreButtonsReal"></tr>
          </table> -->
          <el-row :style="{'width':(moreButtonsReal.length>5?'240px':'80px')}">
            <el-col v-for="(button,$index) in moreButtonsReal" :key="$index" :span="(moreButtonsReal.length>5?8:24)" style="text-align:center">
              <DsfButton
                v-if="showAndHide(button)"
                :key="$index"
                style="width:80px;margin-bottom:5px;margin-top:5px;"
                :icon="getIcon(button)"
                :disabled="button.disabled"
                :size="size"
                :type="button.type"
                :title="button.title"
                :btn-style="'icon-text'"
                @click="clickHandler(button,$event)"
              >{{ button.text }}</DsfButton>
            </el-col>
          </el-row>
        </template>
        <a v-if="moreButtonStyle=='ds-button'" slot="reference" class="auxiliary" :class="[moreButtonStyle]">
          更多
          <i class="iconfont" :class="{'icon-shuangjiantouxia':moreFold,'icon-xiangshangzhanhang':!moreFold}"></i>
        </a>
        <a v-if="moreButtonStyle=='ds-button__icon-text'" slot="reference" :class="[moreButtonStyle]">
          <i class="iconfont icon-gengduo"></i>
        </a>
      </el-popover>
    </span>
  </div>
</template>
<script>
// var esprima = require('esprima');
// window.esprima=esprima;
import sysButton from "../../sysButton";
import control from "_platform/dsf/mixins/control";
/**
 * @class DsfButtonBar
 * @description 按纽组
 * @example
 * <DsfButtonBar></DsfButtonBar>
 */
export default dsf.component({
  name: "DsfButtonBar",
  ctrlCaption: "按纽组",
  mixins: [control],
  /**
   * @memberof DsfButtonBar
   * @name Props
   * @property {Object} [owner = null] 所有者
   * @property {String} [size = ''] 大小
   * @property {String} [align = center] 居中方式
   * @property {Array} [buttons = []] 按钮
   * @property {String} [emitPrefix = ''] 前缀
   * @property {Function} [renderButton = null] 渲染按钮回调掉方法
   */
  props: {
    owner: {
      type: Object,
      default() {
        return null;
      },
    },
    size: {
      type: String,
      default: "",
    },
    align: {
      type: String,
      default: "center",
    },
    buttons: {
      type: Array,
      default() {
        return [];
      },
    },
    emitPrefix: {
      type: String,
      default: "",
    },
    renderButton: {
      type: Function,
      default: null,
    },
  },
  computed: {
    moreButtonsReal() {
      let buttons = []
      _.each(this.moreButtons, (button) => {
        if (this.showAndHide(button)) {
          buttons.push(button)
        }
      })
      return buttons;
    }
  },
  data() {
    return {
      showButtons: [],
      moreButtons: [],
      moreButtonStyle: "",
      moreFold: true
    }
  },
  created() {
  },
  mounted() {
  },
  updated() {
    // console.log(this.buttons)
  },
  methods: {
    showAndHide(button) {
      let result = true;
      if (!this.isDesign) {
        //非设计模式下运行表达式
        if (button.if) {
          result = this.$eval(button.if, this.$local) === false ? false : true;
        }
        else {
          result = true;
        }
      }
      if (this.renderButton) {
        result = this.renderButton(button, result);
      }
      return result;
    },
    clickHandler(button,$event) {
      if (button.disabled||this.isDesign) {
        return;
      }
      let owner = this.owner || this;
      //如果是系统按钮
      if (button.isSystem) {
        let handler = sysButton.handler;
        let params = _.map(button.params, (p) => {
          if (p.value) {
            let rfunction = /^function(\s+\w*)?\(.*\)/
            let v = p.value.trim();
            if (rfunction.test(v)) {
              let code = [
                'debugger;',
                "var result= (" + v + ").call(this,data,button,owner,self);",
                "data=null;",
                "button=null;",
                "owner=null;",
                "self=null;",
                'return result;'
              ]
              let fn = new Function('data', 'button', 'owner', 'self', code.join("\n"));
              let result = fn.call(this.$vm, this.$local, button, owner, this);
              if (dsf.type(result) == "string") {
                result = this.$replace(result, this.$local);
              }
              return result;
            }
            else {
              let v = this.$replace(p.value, this.$local);
              if (v === 'false') {
                v = false;
              }
              else if (v === 'true') {
                v = true;
              }
              return v;
            }
          }
          else {
            return null;
          }
        });
        if (handler[button.actionName]) {
          //系统按钮
          handler[button.actionName].call(this.$vm, {
            owner: owner,
            data: this.$local,
            _self: this,
            targetButton: button,
            params: params
          },$event);
        }
      } else {
        //自定义按钮
        let eventName = (this.emitPrefix ? this.emitPrefix + "-" : '') + button.actionName;
        let hander = owner.$listeners[eventName];
        if (hander && dsf.isFunction(hander)) {
          hander({
            owner: owner,
            data: this.$local,
            targetButton: button,
            _self: this,
          },$event);
        }
      }
    },
    getIcon(button) {
      let icon = button.icon;
      if (icon && icon.startsWith("icon-")) {
        return icon.replace("icon-", "");
      }
      return icon;
    },
    popoverShow() {
      this.moreFold = false;
    },
    popoverClose() {
      this.moreFold = true;
    },
    //触发指定按钮的点击事件
    triggerButtonClick(button) {
      this.clickHandler(button);
    }
  },
  watch: {
    buttons: {
      handler(v) {
        this.showButtons = [];
        this.moreButtons = [];
        _.each(v, (button) => {
          !button.isMore && this.showButtons.push(button)
        });
        _.each(v, (button) => {
          button.isMore && this.moreButtons.push(button);
        });
        if (this.moreButtons.length > 0) {
          this.moreButtonStyle = this.moreButtons[0].btnStyle ? "ds-button__" + this.moreButtons[0].btnStyle : "ds-button";
        }
      },
      immediate: true
    }
  }
});
</script>
