<template>
  <div class="ds-panel">
    <div v-if="hasHeader" class="ds-panel-header">
      <div v-if="hasTitle" class="title">
        <label class="ds-font3" :style="{'border-right': titleBorder?'1px solid #eeeeee;':'0px none'}">
          <slot name="title"></slot>
        </label>
      </div>
      <div class="header">
        <slot name="header"></slot>
      </div>
    </div>
    <div class="ds-panel-body">
      <slot></slot>
    </div>
  </div>
</template>
<script>
export default {
  name: 'DsfPanel',
  props: {
    hasTitle:{
      type:Boolean,
      default:true
    },
    hasHeader:{
      type:Boolean,
      default:true
    },
    hasFooter: {
      type: Boolean,
      default: false
    },
    titleBorder:{
       type: Boolean,
      default: false
    }
  }
}
</script>