<template>
  <div class="page-home-header">
    <div class="logo-box">
      <slot name="logo">
        <img v-if="logo.image" :src="logo.image" alt="" class="logo">
        <div v-if="logo.title" class="site-name">{{ logo.title }}</div>
      </slot>
    </div>
    <div class="header-right">
      <div class="header-menu">
        <div v-for="(item, index) in menuList" :key="item.menu_id" :class="{ active: item.menu_id === activeKey }" class="header-menu-item" @click="linkTo(item, index)">
          <div class="header-menu-item-box">
            <i :class="[ item.menu_icon ]" class="iconfont"></i>
            <div>{{ item.menu_name }}</div>
          </div>
        </div>
      </div>
      <div class="header-user">
        <el-dropdown :show-timeout="0" placement="bottom" @visible-change="showDropdown = $event" @command="handleCommand">
          <div>
            <div class="header-user-icon" :style="{'background-image': `url(${defaultHeader})`}">
              <img v-show="showIcon" :src="headIcon" @error="headLoadError" @load="headLoadSuccess" />
            </div>
            <div class="username">
              欢迎您,{{ dsf.cookies.get("user_name") }}
              <i v-if="dropdownConfig.length" :class="[showDropdown ? 'icon-shangjiantou' : 'icon-xiajiantou']" class="iconfont" />
            </div>
          </div>
          <el-dropdown-menu v-if="dropdownConfig.length" slot="dropdown">
            <el-dropdown-item v-for="(item, index) in dropdownConfig" :key="item.key" :command="index">
              {{ item.name }}
            </el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'DsfHomeHeader',
  inject: ['root'],
  props: {
    dropdownConfig: {
      type: Array,
      default() {
        return []
      }
    }
  },
  data() {
    return {
      logo: window.$$config.logo,
      headIcon: '',
      defaultHeader: window.$$config.user.defaultHeader,
      showIcon: false,
      showDropdown: false
    }
  },
  computed: {
    menuList() {
      return this.root.menuList || [];
    },
    activeKey() {
      if (this.root.menuKey.length > 0) {
        return this.root.menuKey[0];
      }
      return '';
    }
  },
  created() {
    this.initHeadIcon();
  },
  methods: {
    // 初始化头像，从cookie中获取
    initHeadIcon() {
      const headIcon = dsf.getCookie('photoRelativePath');
      if (headIcon) {
        this.headIcon = decodeURIComponent(headIcon);
      }
    },
    // 头像加载成功时
    headLoadSuccess() {
      this.showIcon = true;
    },
    // 头像加载失败时
    headLoadError() {
      this.showIcon = false;
    },
    linkTo(item, index) {
      if (this.activeIndex === index) return;
      window.location.href = this.getUrl(item);
    },
    getUrl(item) {
      if (item.menu_url) {
        return item.menu_url;
      } else if (item.children && item.children.length) {
        if (item.children[0].menu_url) {
          return item.children[0].menu_url
        } else {
          return this.getUrl(item.children[0])
        }
      }
      return '';
    },
    // 用户头像下的菜单点击
    handleCommand(index) {
      const item = this.dropdownConfig[+index];
      this.$emit('dropdown-click', item);
    }
  }
}
</script>