<template>
  <div
    v-if="isDesign"
    class="ds-control ds-form-item ds-design-hide ds-textbox"
    :class="getCss"
  >
    <template>
      <label v-if="showLabel" class="ds-form-label">
        流程信息
      </label>
      <div class="ds-form-block">
        <el-input v-model="chooseFlow.name" type="text" :disabled="true" />
      </div>
    </template>
  </div>
</template>
<script>
import formControl from "../../../mixins/formControl";
export default dsf.component({
  name: "DsfFlowInfo",
  mixins: [formControl],
  ctrlCaption: "流程信息",
  props: {
    caption: {
      type: String,
      default() {
        return "wfinfo";
      },
    },
    chooseFlow: {
      type: Object,
      default() {
        return {};
      },
    },
    eventName: {
      type: String,
      default() {
        return "";
      },
    },
    linkData: {
      type: Array,
      default() {
        return [];
      },
    },
    metadata: {
      type: Object,
      default() {
        return dsf.metadata.get("flow-info-meta-data");
      }
    }
  },
  data() {
    return {
      _RequestID: null
    }
  },
  watch: {
    chooseFlow(val) {
      const value = {
        wf_id: val.ID,
				wf_name: val.name
      }
      this.emitValueChange(value);
    },
  },
  computed: {},
  mounted() {
    // console.log("挂载文本")
  },
  destroyed() {
    // console.log("文本框被销毁")
  },
  methods: {
    linkSetChooseFlow() {
      document.querySelector("[a='linkData2'] .ds-button").click();
    },
    flowSend(res) {
      // 流程发送按钮
      let id = this.chooseFlow.dsfa_rm_id;
      if (id) {
        // 开始发送
        let _this = this,
          // loadingIndex = dsf.layer.loadding(),
          url = "wfr/startSend",
          param = {
            sWFID: id,
            sObjectID: res._id,
            sObjectName: this.eventName || '',
            sCurNodeID: "",
            sExInfos: ""
          };

        //继续发送
        if (this.$route.query.sPID) {
          url = 'wfr/send';
          param = {
            sPID: this.$route.query.sPID,
            sCurLinkID: this.$route.query.sCurLinkID || '',
            sExInfos: ""
          }
        }
        
        if(this.$vm && this.$vm.flows && this.$vm.flows.disposeIdea) {
          let disposeIdea = this.$vm.flows.disposeIdea;
          // 发送时有处理意见
          param.sOpinion = {
            'Content': disposeIdea.inputValue,
            'Attachments': disposeIdea.multipleFiles
          }
          param.sOpinion = JSON.stringify(param.sOpinion);
        }
        let config = {
          param : param,
          url: url
        }
        console.log(config);
        
        // if(this.event.has(flowCtrlEvent.setSendConfig)){
        //   // 可以通过二次开发修改发送的接口的参数
        //   this.event.trigger(flowCtrlEvent.setSendConfig, { ui: this, config: config });
        // }

        this.$http.post(config.url, config.param)
          .then(response => {
            this.handler(response, []);
          })
          .catch(response => {
            dsf.layer.message("发送异常", false);
            this._RequestID = null;
          })
          .finally(() => {
            // dsf.layer.close(loadingIndex);
          });
      } else {
        dsf.layer.message("请先配置流程信息控件的'选择流程'", false);
      }
    },
    handler(response, layerIndexs) {
      if (response.data.state == 20000) {
        let data = response.data.data;
        this._RequestID = data.RequestID;
        if (data.Code == 20000 || data.Code == 20001 || data.Code == 20003 || data.Code == 20004 || data.Code == 20005) {
          // 发送成功 || 发送到汇总节点 || 发送到汇总环节并激活汇总环节 || 办结当前节点 || 办结当前节点，并办结整个流程
          this.directlySend(data);
          // this.root.close('3000');
          this.event.trigger(flowCtrlEvent.changeState, {
            data: data,
            type: '发送'
          })
        } else if (data.Code == 81000) {
          // 或选择
          this.chooseSendNode(data);
        } else if (data.Code == 82000) {
          // 与选择
          this.chooseSendTree(data);
        } else {
          let msg = null,
            code = data.Code;
          switch (code) {
            case 20006:
              msg = "流程已被销毁"
              break;
            case 20007:
              msg = "流程已被强制办结"
              break;
            case 91000:
              msg = "无后续可发送节点"
              break;
            case 92000:
              msg = "无可选的发送对象"
              break;
            case 92001:
              msg = "该环节只允许发送给一人，但是必选项超过一个"
              break;
            case 92002:
              msg = "未找到继承环节"
              break;
            case 92003:
              msg = "对象超出可发送范围"
              break;
            case 92004:
              msg = "路径超出可发送范围"
              break;
            case 93000:
              msg = "该事项已被处理"
              break;
            case 93001:
              msg = "保存流转情况信息失败"
              break;
            case 93002:
              msg = "保存流转环节信息失败"
              break;
            case 93003:
              msg = "该文件已被其他人签收"
              break;
            case 94001:
              msg = "与分支不能设置办结节点"
              break;
            case 95001:
              msg = "没有可回收的环节"
              break;
            case 95002:
              msg = "回收环节已接收或已处理，无法回收"
              break;
            case 95003:
              msg = "未填写退回原因"
              break;
            case 99000:
              msg = "获取数据锁失败"
              break;
          }
          dsf.layer.message(msg || '未知流程类型发送', false);
        }
      } else {
        this._RequestID = null;
        dsf.layer.message("发送异常", false);
      }
    },
    directlySend(data) {
      let userHtml = "",
        _this = this;

      if (data.Code == 20000) {
        _.forEach(data.Links, (line, index) => {
          if (line.Status == 0 && line.LinkUser) {
            let linkUser = line.LinkUser;
            let type = line.LinkUser.Type;
            if (type == 1) {
              // 直接选人
              userHtml += "<span>" + linkUser.UserName + "</span>";
            } else if (type == 2) {
              // 部门+角色
              userHtml += "<span>" + linkUser.DeptName + "-" + linkUser.RoleName + "</span>";
            } else if (type == 3) {
              // 岗位
              userHtml += "<span>" + linkUser.JobName + "</span>";
            } else if (type == 4) {
              // 人+关系
              userHtml += "<span>" + linkUser.UserName + "-" + linkUser.RelationName + "</span>";
            }
          }
        })
        dsf.layer.message(`<div class='userbox'></div>
        <div>页面将在<span  class='timebox'>${3}</span>秒钟后自动关闭</div>`)
        // layui.layer.open({
        //   type: 1,
        //   title: "流程已发送到以下人员：",
        //   skin: 'flowinfo-dialog',
        //   area: '300px;',
        //   id: _this._RequestID,
        //   time: 3000,
        //   btn: ['好的'],
        //   btnAlign: 'c',
        //   content: "<div class='userbox'></div><div>页面将在<span  class='timebox'>3</span>秒钟后自动关闭</div>",
        //   success: function(layero) {
        //     let timebox = layero.find('.timebox'),
        //       userbox = layero.find('.userbox'),
        //       time = 3;

        //     setInterval(() => {
        //       $(timebox).html(--time);
        //     }, 1000)

        //     $(userbox).html(userHtml);
        //   }
        // });
      } else {
        let msg = '发送成功',
          code = data.Code;
        switch (code) {
          case 20004:
            msg = "文件已办结"
            break;
          case 20005:
            msg = "文件已办结"
            break;
        }
        dsf.layer.message(`<div class='userbox'>
          <span>${msg}</span>
        </div>
        <div>页面将在<span  class='timebox'>${3}</span>秒钟后自动关闭</div>`)
        // dsf.layer.message({
        //   title: "提示",
        //   content: "<div class='userbox'><span>" + msg +
        //     "</span></div><div>页面将在<span  class='timebox'>3</span>秒钟后自动关闭</div>",
        //   success: function(layero) {
        //     let timebox = layero.find('.timebox'),
        //       time = 3;

        //     setInterval(() => {
        //       $(timebox).html(--time);
        //     }, 1000)
        //   }
        // });
      }

    },
    getSendBackLink(args) {
      let param = {
        sPID: args[0] || null,
        sCurLinkID: args[1] || null
      }
      this.$http.post(dsf.url.getWebPath('wfr/getSendBackLink'), param)
        .then(res => {
          if (res.data.success && res.data.data && res.data.data.BackLinks) {
            let BackLinks = res.data.data.BackLinks;
            if (BackLinks.length == 1) {
              this.sendBack(args, BackLinks[0].ID);
            } else {
              chooseBackNode.call(this, BackLinks, args);
            }
          } else {
            dsf.layer.message(res.data.Message || '退回异常', false);
          }
        })
        .catch(res => {
          dsf.layer.message('退回异常', false);
        })
        .finally(() => {
          // dsf.layer.close(loadingIndex);
        });
    },
    sendBack(args, id, layerIndexs) {
      let _this = this;
      dsf.layer.prompt('', '退回原因', {
          inputPlaceholder: '请输入退回原因',
          inputType: 'textarea',
          inputPattern: /\S/,
          inputErrorMessage: '退回原因不能为空',
          confirmButtonText: '确定',
        }).then(({ value }) => {
          let param = {
              sPID: args[0] || null,
              sCurLinkID: args[1] || null,
              sReason: value || null,
              sBackLinkID: id
            }
            this.$http.post(dsf.url.getWebPath('wfr/sendBack'), param)
              .then(res => {
                if (res.data.success && res.data.data && res.data.data.Code == '21000') {
                  dsf.layer.message('退回成功', true);
                } else {
                  dsf.layer.message(res.data.Message || '退回异常', false);
                }
              })
              .catch(res => {
                dsf.layer.message('退回异常', false);
              })
              .finally(() => {
                // dsf.layer.close(loadingIndex);
              });
        });
    },
    chooseSendNode(data) {
      dsf.layer.pc.openDialog({
        title: "请选择要发送的环节：",
        width: "300px",
        height: "300px",
        overflow: "hidden",
        content: "ChooseSendNode",
        params: {
          lines: data.SendParams.Lines,
        },
        btns: [
          {
            text: "确定",
            handler: (component) => {
              if (component.validate()) {
                const line = component.yes();
                let param = {
                  sRequestID: this._RequestID,
                  sLineID: line.LineID
                };
                this.$http
                  .post("wfr/sendToLine", param)
                  .then((res) => {
                    if (res.data.state == 20000) {
                      dsf.layer.message('发送成功');
                    }
                  });
                return true;
              }
              return false;
            },
          },
          {
            text: "取消",
          },
        ],
      });
    },
    chooseSendTree(data) {
      dsf.layer.pc.openDialog({
        title: "请选择办理人",
        width: "900px",
        height: "420px",
        overflow: "hidden",
        content: "ChooseSendTree",
        params: {
          lines: data.SendParams.Lines,
        },
        btns: [
          {
            text: "确定",
            handler: (component) => {
              if (component.validate()) {
                const result = component.yes();
                let param = {
                  sRequestID: this._RequestID,
                  sReceivers: result
                };
                this.$http
                  .post("wfr/sendToReceivers", param)
                  .then((res) => {
                    if (res.data.state == 20000) {
                      // this.flowSend(res);
                      dsf.layer.message("发送成功");
                    }
                  });
                return true;
              }
              return false;
            },
          },
          {
            text: "取消",
          },
        ],
      });
    },
  }
});
</script>
