<template>
  <div v-if="isDesign" class="ds-control" style="text-align:center;background-color:#fff;padding:3px 0px;font-style:italic;color:#c0c0c0">
    事项选择
  </div>
  <div v-else class="ds-control ds-one-thing-form-select">
    <div class="ds-one-thing-form-select-top" @click="openDialog">
      <span>事项选择</span>
      <i class="iconfont icon-ln_jiantouyou"></i>
    </div>
    <div class="ds-one-thing-form-select-body">
      <ul>
        <template v-for="({value, text, itemId}, index) in matter">
          <li :key="value" :class="{bofore: activeIndex > index, active: activeIndex === index}">
            <i></i><i></i>
            <a @click="scrollTo(itemId, index)">{{ text }}</a>
          </li>
        </template>
      </ul>
    </div>
  </div>
</template>

<script>
import formControl from "_platform/dsf/mixins/formControl";
import FormSelectItem from './item/FormSelectItem';

export default dsf.component({
  ctrlCaption: "办理事项",
  mixins: [formControl],
  name: "DsfOneThingFormSelect",
  components: {
    FormSelectItem
  },
  props: {
    marginTop: {
      type: Boolean,
      default: false
    },
    marginBottom: {
      type: Boolean,
      default: false
    },
    value: {
      type: Object,
      required: false
    },
    metadata: {
      type: Object,
      default() {
        return dsf.metadata.get("one-thing-form-select-meta-data");
      }
    }
  },
  data () {
    return {
      activeIndex: 0,
      showBt: false,
      scene: {
        typeid: '',
        type: ''
      },
      matter: []
    }
  },
  computed: {

  },
  watch: {
    'value' () {
      this.init();
    }
  },
  created () {
    this.init();
  },
  mounted () {
    if (!this.isDesign) {
      let $win = $('.ds-page-home-main');
      this.isHome = !!$win.length;
      this.$win = this.isHome ? $win.eq(0) : $('html, body');
      document.body.appendChild(this.$el);
      this.isHome ?
        this.$win.get(0).addEventListener('scroll', this.onScroll) :
        window.addEventListener('scroll', this.onScroll);
      window.addEventListener('resize', this.onScroll);
    }
  },
  destroyed () {
    this.isHome ?
      this.$win.get(0).removeEventListener('scroll', this.onScroll) :
      window.removeEventListener('scroll', this.onScroll);
    window.removeEventListener('resize', this.onScroll);
  },
  methods: {
    init() {
      if (!this.value) {
        this.$emit('change', {
          type: '',
          typeid: '',
          text: '',
          itemids: '',
          value: ''
        });
        return;
      }
      let {type, typeid, text, itemids, value, code, taskHandleItem} = this.value;
      text = text.split(',');
      itemids = itemids.split(',');
      value = value.split(',');
      code = code.split(',');
      taskHandleItem = taskHandleItem.split(',');
      let matter = [{
        text: '基本信息',
        itemId: '基本信息',
        value: '基本信息'
      }];
      value.forEach((v, i) => {
        matter.push({
          text: text[i],
          itemId: itemids[i],
          value: v,
          code: code[i],
          taskHandleItem: taskHandleItem[i]
        });
      })
      this.scene = {typeid, type};
      this.matter = matter;
    },
    openDialog() {
      this.$openDialog({
        title:"并联办事信息",
        width: '935px',
        content: 'DsfOneThingSceneSelect',
        params: {
          metaData: this.value
        },
        btns: [
          {
            text: "确定",
            handler: res => {
              const data = res.yes();
              if (data === null) {
                dsf.layer.message('请选择场景事项', false);
                return false;
              }
              this.emitValueChange(data);
            },
          },
          {
            text: "取消",
          },
        ],
      })
    },
    scrollTo(itemId) {
      if (itemId === '基本信息') {
        this.$win.animate({
          scrollTop: 0
        }, 200, () => {
          this.onScroll();
        });
        return;
      }
      const $el = $(`.ds-section[item-id="${itemId}"]`);
      if ($el.get(0)) {
        let scrollTop = this.isHome ?
          $el.offset().top + this.$win.scrollTop() - this.$win.offset().top :
          $el.offset().top;
        this.$win.animate({
          scrollTop
        }, 200, () => {
          this.onScroll();
        });
      }
    },
    onScroll() {
      const st = this.$win.scrollTop();
      const wh = this.$win.height() - 85;
      const wt = this.$win.offset().top;
      let activeIndex = 0;
      this.matter.forEach(({itemId}, index) => {
        const $el = $(`.ds-section[item-id="${itemId}"]`);
        if ($el.get(0)) {
          const eh = $el.height();
          const et = this.isHome ? $el.offset().top + st - wt : $el.offset().top;
          if (et + eh > st && et < st + wh && activeIndex < index) {
            activeIndex = index;
          }
        }
      });
      this.activeIndex = activeIndex;
    }
  }
});
</script>
