<template>
  <div id="app">
    <home :is-home="isHome" :is-true="isTrue"></home>
  </div>
</template>

<script>
import home from './views/Home'
import { mapState } from 'vuex';
export default {
  components: {
    home
  },
  data() {
    return {
      isHome: false,
      themes: dsf.config.defaultTheme,
      editors: [],
      isTrue: false, // 根据路由不同切换不一样的头部
    };
  },
  computed: {
    ...mapState([
      'route'
    ])
  },
  watch: {
    'route' () {
      const path = ['/pc/ot/otherpage/home']
      this.isTrue = path.indexOf(this.route.fullPath) > -1 ? true : false
    },
  },
  created() {
    let htmlName = window.location.pathname;
    if (htmlName.indexOf("/index.html") >= 0) {
      this.isHome = true;
    }
  },
  mounted() {
    //加载皮肤
    document.body.classList.add(this.themes);
  },
  methods: {

  }
};
</script>