<template>
  <div v-if="!isDesign" class="ds-control ds-viewpart ds-no-padding" :class="getCss" :path="getPath()" :style="{'background-color':backgroundColor}">
    <component :is="componentName" v-if="componentName" ref="view" :name-space="nameSpace" :page-name="pageName" :query-string="params" v-bind="$attrs"></component>
  </div>
  <div v-else class="ds-control ds-viewpart ds-no-padding" :class="getCss">
    <div class="ds-viewparent-desgine-panel">
      <span>{{ path }}页面片段</span>
    </div>
  </div>
</template>
<script>
import control from "_platform/dsf/mixins/control";
import dynamicComponent from '_platform/dsf/output/dynamicComponent'
export default dsf.component({
  inheritAttrs: false,
  name: "DsfViewPart",
  ctrlCaption: "页面视图",
  mixins: [control],
  provide() {
    return {
      $frame: this
    }
  },
  props: {
    path: {
      type: String,
      default: ""
    },
    showModel: {
      type: String,
      default: "none"
    },
    backgroundColor: {
      type: String,
      default: ""
    },
    forceUpdate: {
      type: Boolean,
      default: true
    }
  },
  data() {
    return {
      componentName: "",
      nameSpace: "",
      pageName: "",
      params: {}
    }
  },
  created() {
    if (this.$vm && this.$vm.frames) {
      this.$vm.frames[this.caption] = this;
    }
  },
  computed: {
    getCss() {
      return [
        ...this.$getClass(),
        this.shadow ? 'shadow' : '',
        this.showModel == 'full' ? 'full' : ''
      ]
    }
  },
  updated() {
    if (this.isDesign) {
      this.$dispatch('design-height-change', this.showModel == 'full' ? "100%" : 'auto');
    }
  },
  methods: {
    getPath() {
      return this.$replace(this.path)
    },
    reset() {
      this.$refs.view.reset();
    },
    reloadData() {
      this.$refs.view.reloadData();
    },
    yes() {
      if (this.$refs.view.yes) {
        return this.$refs.view.yes();
      }
    }
  },
  watch: {
    "path": {
      handler(v) {
        if (v && !this.isDesign) {
          let paramsStart = this.path.indexOf("?");
          var arr = [
            this.path.substr(0, (paramsStart < 0 ? this.path.length : paramsStart)),
            paramsStart < 0 ? "" : this.path.substr(paramsStart + 1),
          ]
          var path = arr[0];
          var params = arr[1];
          let pathArr = path.split("/");
          let ns = path.replace(/\//g, '.')
          this.nameSpace = pathArr.slice(0, pathArr.length - 1).join(".");
          this.pageName = pathArr[pathArr.length - 1];
          let parseResult = dsf.url.parse(params);
          //动态替换表达式
          _.each(parseResult, (param, i) => {
            this.params[i] = this.$replace(param)
          })
          dynamicComponent(ns, "pc")
            .then((result) => {
              if (this.componentName == result.name) {
                if (this.forceUpdate) {
                  this.componentName = "";
                  this.$nextTick(() => {
                    this.componentName = result.name;
                  })
                }
                else {
                  this.reset();
                  this.reloadData();
                }
              }
              else {
                this.componentName = "";
                this.$nextTick(() => {
                  this.componentName = result.name;
                })
              }
            })
            .catch((error) => {
              dsf.layer.pc.message("无法加载组件", false)
            })
        }
      },
      immediate: true
    }
  }
});
</script>