<template>
  <div class="ds-control ds-form-item ds-textbox" :class="getCss">
    <template v-if="simple&&readOnly&&!isDesign">
      {{ value }}
      <div v-if="errors.length>0" class="ds-error-text">{{ errorsMsg }}</div>
    </template>
    <template v-else>
      <label v-if="showLabel" class="ds-form-label" :style="getLabelWidthStyle()">
        {{ label }}
        <i v-if="dataCapion" class="iconfont icon-bangzhuzhongxin ds-control-helper" @click="showHelper"></i>
      </label>
      <div class="ds-form-block" :style="getFormItemBlockMarginLeft()">
        <el-autocomplete
          v-if="!readOnly"
          v-model="inputValue"
          :fetch-suggestions="querySearchAsync"
          :placeholder="placeholder"
          :disabled="disabled"
          :trigger-on-focus="false"
          :debounce="delay"
          value-key="label"
          :class="{'ds-error-border':errors.length>0}"
          @select="handleSelect"
          @blur="handleBlur"
          @change="handleChange"
        ></el-autocomplete>

        <!--只读状态只显示div-->
        <div v-else-if="readOnly" class="ds-form-readonly">
          <span>{{ value }}</span>
        </div>
        <div v-if="errors.length>0" class="ds-error-text">{{ errorsMsg }}</div>
      </div>
    </template>
  </div>
</template>
<script>
  import formControl from "../../../mixins/formControl";

  export default dsf.component({
    name: "DsfAutoComplete",
    mixins: [formControl],
    ctrlType: "autocomplete",
    ctrlCaption: "自动填充",
    props: {
      simple: {
        type: Boolean,
        default: false
      },
      openInputGrade: {
        type: Boolean,
        default: false
      },
      placeholder: {
        type: String,
        default: "请输入"
      },
      metadata: {
        type: Object,
        default() {
          return dsf.metadata.get("text-meta-data");
        }
      },
      metaDataType: {
        type: String,
        default: "0"
      },
      delay: {
        type: Number,
        default: 300
      },
      // chooseMetaData: {
      //   type: Object,
      //   default: {}
      // },
      source: {
        type: String,
        default: ""
      },
      maxItemsNumber: {
        type: Number,
        default: 20
      },
      autoSwitch: {
        type: Boolean,
        default: true
      }
    },
    computed: {
      inputValue: {
        get() {
          return this.value;
        },
        set(v) {
          this.emitValueChange(v);
        }
      }
    },
    mounted() {

    },
    updated() {
    },
    destroyed() {
      // console.log("文本框被销毁")
    },
    methods: {
      querySearchAsync(v, cb) {
        const self = this;
        const url = dsf.express.replace(self.source);
        self.$http.get(url, {q: v})
          .done(res => {
            if (!res.success) {
              dsf.layer.message(res.message, false);
              return
            }
            let result = res.data.slice(0, self.maxItemsNumber + 1);

            _.forEach(result, item => {
              item.label = item.label || item.value;
            });

            cb(result);
          })
          .error(err => {
            dsf.layer.message("查询输入建议报错", false)
          })
      },
      handleBlur() {
        this.$dispatch("blur");
        this.emitFormValidate(this.value);
      },
      handleSelect(v) {
        this.$dispatch("select", v);
        this.inputValue = v.value;

        //填充数据到其它控件
        if(this.$vm){
          let dict = this.$vm.getScopeMetaDataDict(this);

          if(this.$local){
            _.forOwn(v, (val, key) => {
              _.forOwn(dict, (dictVal, dictKey) => {
                if (dictVal.fieldsLength === 1) {
                  if (dictKey === key){
                    this.$vm.$set(this.$local, dictKey, val);
                  } else {
                    let last = dictKey.lastIndexOf(".");
                    let lastStr = dictKey.substring(last + 1, dictKey.length);
                    lastStr === key && this.$vm.$set(this.$local, dictKey, val);
                  }
                }
              })
            })
          }
        }

        //验证v.value
        this.$nextTick(() => {
          this.emitFormValidate(v.value);
        })
      },
      handleChange(v) {
        this.inputValue = v;
      }
    }
  });
</script>
