<template>
  <div class="ds-control ds-form-item ds-textbox" :class="getCss">
    <template v-if="simple&&readOnly&&!isDesign">
      <span :title="value|proxy(formatter)">{{ value|proxy(formatter) }}</span>
      <div v-if="errors.length>0" class="ds-error-text">{{ errorsMsg }}</div>
    </template>
    <template v-else>
      <label v-if="showLabel" class="ds-form-label" :style="getLabelWidthStyle()">
        {{ label }}
        <i v-if="dataCapion" class="iconfont icon-bangzhuzhongxin ds-control-helper" @click="showHelper"></i>
      </label>
      <div class="ds-form-block" :style="getFormItemBlockMarginLeft()">
        <el-input v-if="!readOnly" v-model="inputValue" :class="{'ds-error-border':errors.length>0}" :placeholder="placeholder" :type="inputType" :disabled="disabled" @change="inputChangeHandler" @blur="inputBlurHandler" />
        <!--只读状态只显示div-->
        <div v-else-if="readOnly" class="ds-form-readonly">
          <span :title="value|proxy(formatter)">{{ value|proxy(formatter) }}</span>
        </div>
        <div v-if="errors.length>0" class="ds-error-text">{{ errorsMsg }}</div>
        <!-- <div class="ds-error-text">sdfsfsdfds</div> -->
      </div>
    </template>
  </div>
</template>
<script>
import formControl from "../../../mixins/formControl";
export default dsf.component({
  name: "DsfTextbox",
  mixins: [formControl],
  ctrlCaption: "文本框",
  props: {
    simple: {
      type: Boolean,
      default: false
    },
    ishideCon: {
      type: Boolean,
      default: false
    },
    openInputGrade: {
      type: Boolean,
      default: false
    },
    placeholder: {
      type: String,
      default: "请输入"
    },
    metadata: {
      type: Object,
      default() {
        return dsf.metadata.get("text-meta-data");
      }
    },
    pinyin:{
      type:Boolean,
      default(){
        return false
      }
    }
  },
  computed: {
    inputType() {
      return this.ishideCon ? "password" : "text";
    },
    inputValue: {
      get() {
        return this.value;
      },
      set(v) {
        this.emitValueChange(v);
      }
    }
  },
  mounted() {
    // console.log("挂载文本")
  },
  updated() {
    // console.log("修改文本")
  },
  destroyed() {
    // console.log("文本框被销毁")
  },
  methods: {
    inputChangeHandler() {
      this.$dispatch("change",this.value);
      this.emitFormValidate(this.value);
    },
    inputBlurHandler() {
      this.$dispatch("blur");
    }
  }
});
</script>
