<template>
  <div class="page-home-header">
    <div class="logo-box">
      <h1 class="title">一件事改革服务平台<span class="sub-text">上海市公务员职业生涯全周期管理</span></h1>
    </div>
    <div class="header-right">
      <div class="right-menu">
        <div class="search">
          <el-input v-model="search" placeholder="输入关键字" class="input-with-select">
            <el-select slot="prepend" v-model="select">
              <el-option label="全部" value="999"></el-option>
              <el-option label="应用" value="1"></el-option>
              <el-option label="阅读" value="2"></el-option>
              <el-option label="消息" value="3"></el-option>
            </el-select>
            <el-button slot="append">搜索<i class="el-icon-search el-icon--right"></i></el-button>
          </el-input>
        </div>
        <div class="user-text user">{{ dsf.cookies.get("user_name") }}</div>
        <div class="user-text logout" @click="loginOut">注销</div>
      </div>
    </div>
  </div>
</template>

<script>
import control from '_platform/dsf/mixins/control'
export default dsf.component({
  ctrlCaption: '首页头部',
  mixins: [control],
  name: "DsfOneThingHomeHeader",
  data() {
      return {
          search: '',
          select: '999',
      }
  },
  methods: {
    // 退出登录
    loginOut() {
      this.$webAPI.loginOut()
        .then(({ success }) => {
          dsf.cookies.set('authorization_token', '');
          dsf.cookies.set('user_name', '');
          dsf.cookies.set('user_id', '');
          dsf.cookies.set('isLogin', '');
          dsf.storage.session.clear();
          location.replace('./page.html#/pc/login');
        })
        .catch((ex) => {
          dsf.layer.message(ex, false);
        })
    }
  }
});
</script>