<template>
  <div class="ds-control ds-form-item ds-select" :class="getCss">
    <template v-if="simple && readOnly && !isDesign">
      <span :title="value|proxy(formatter)">{{ value|proxy(formatter) }}</span>
      <div v-if="errors.length>0" class="ds-error-text">{{ errorsMsg }}</div>
    </template>
    <template v-else>
      <label v-if="showLabel" class="ds-form-label" :style="getLabelWidthStyle()">
        {{ label }}
        <i v-if="dataCapion" class="iconfont icon-bangzhuzhongxin ds-control-helper" @click="showHelper"></i>
      </label>
      <div class="ds-form-block" :style="getFormItemBlockMarginLeft()">
        <el-select
          v-if="!readOnly"
          v-model="selectValue"
          :disabled="disabled"
          :placeholder="placeholder"
          :collapse-tags="collapseTags"
          :multiple-limit="multipleLimit"
          :multiple="multiple"
          :filterable="filterable || allowCreate"
          :allow-create="allowCreate"
          :class="{'ds-error-border':errors.length>0}"
        >
          <el-option label="" value="">
            <span style="color: #c0c0c0;">请选择</span>
          </el-option>
          <template v-if="isDesign">
            <el-option label="数据1" value="1"></el-option>
            <el-option label="数据2" value="2"></el-option>
            <el-option label="数据3" value="3"></el-option>
            <el-option label="数据4" value="4"></el-option>
            <el-option label="数据5" value="5"></el-option>
          </template>
          <template v-else>
            <el-option v-for="option in $items" :key="option.value" :label="option.text" :value="option.value"></el-option>
          </template>
        </el-select>
        <!--只读状态只显示div-->
        <div v-else-if="readOnly" class="ds-form-readonly">
          <span>{{ value|proxy(formatter) }}</span>
        </div>
        <div v-if="errors.length>0" class="ds-error-text">{{ errorsMsg }}</div>
      </div>
    </template>
  </div>
</template>
<script>
import formControl from "../../../mixins/formControl";

/**
 * @class DsfSelects
 * @augments FormControl
 * @description 多选下拉框
 * @example
 * <DsfSelects :items="[{ text: "1", value: "1" }]" :value="value"></DsfSelects>
 */
export default dsf.component({
  name: "DsfSelect",
  mixins: [formControl],
  ctrlCaption: "下拉框",
  /**
   * @memberof DsfSelects
   * @name Props 属性
   * @property {Boolean} [simple = false] 是否是简单模式
   * @property {String} [placeholder = ''] 占位符提示
   * @property {Array} [value = []] 选择值
   * @property {DsfSelectsItem[]} [items = [{ text: "1", value: "1" }]] 选项集合
   * @property {Number} [maxChooseNum = 0] 最大选择数量
   * @property {Boolean} [collapseTags = false] 折叠标签
   * @property {Object} [metadata = dsf.metadata.get("items-meta-data")] 元数据
   */
  /**
   * @namespace DsfSelectsItem
   * @description 多选下拉的选项
   * @property {String} [DsfSelectsItem.text = ''] 显示值
   * @property {String} [DsfSelectsItem.value = ''] 选中值
   */
  props: {
    simple: {
      type: Boolean,
      default: false,
    },
    placeholder: {
      type: String,
      default: "请选择",
    },
    // 值
    value: {
      type: [Object, String, Number, Array],
      required: false
    },
    // 选项
    items: {
      type: Array,
      default() {
        return [];
      }
    },
    filterable: {
      type: Boolean,
      default: false,
      desc: "是否可搜索"
    },
    multiple: {
      type: Boolean,
      default: false,
      desc: "是否多选"
    },
    allowCreate: {
      type: Boolean,
      default: false,
      desc: "是否可创建条目"
    },
    maxChooseNum: {
      type: Number,
      default: 0,
      desc: "多选时，选择上限，0为不限制"
    },
    collapseTags: {
      type: Boolean,
      default: false,
      desc: "多选时，选中值是否以合并展示"
    },
    formatter: {
      type: String,
      default: "textValue^text"
    },
    metadata: {
      type: Object,
      default() {
        return dsf.metadata.get("items-meta-data");
      },
    },
  },
  data() {
    return {
      valueCopy: []
    }
  },
  computed: {
    selectValue: {
      get() {
        if (this.multiple) {
          if (dsf.type(this.valueCopy) === "array") {
            return this.valueCopy.map(v => v.value);
          }
          return [];
        } else {
          if (dsf.type(this.valueCopy) === "array" && this.valueCopy.length) {
            return this.valueCopy[0].value;
          }
          return '';
        }
      },
      set(v) {
        this.initValue(v);
      }
    },
    $items() {
      let items = this.items;
      //如果选项比值后获取到的情况下，重新更新一下值
      this.initValue(this.value);
      return items;
    },
    multipleLimit() {
      return this.maxChooseNum > 0 ? this.maxChooseNum : 0;
    }
  },
  watch: {
    'value': {
      handler(to) {
        this.initValue(to);
      },
      immediate: true
    },
    'multiple'(to, from) {
      if (!to && from) {
        this.selectValue = this.valueCopy.length ? this.valueCopy[0] : [];
      }
    }
  },
  created() {
    this.initValue(this.value);
  },
  mounted() {
    // console.log("挂载下拉")
  },
  updated() {
    // console.log("修改下拉")
  },
  methods: {
    initValue(to) {
      let nv;
      if (to === void 0 || to === null) {
        nv = [];
      } else if (dsf.type(to) === "array") {
        nv = this.filterValue(to);
      } else {
        nv = this.filterValue([to]);
      }
      if (!this.isSame(nv, this.valueCopy)) {
        this.valueCopy = nv;
        this.updateValue(nv);
      }
    },
    filterValue(value) {
      let newValue = [];
      value.filter(v => {
        let _v = void 0;
        if (typeof v === "object" && v.value !== void 0) {
          _v = this.getObjectByValue(v.value);
        } else if (typeof v === "string" ||
          typeof v === "number") {
          _v = this.getObjectByValue(v);
        }
        if (_v !== void 0) newValue.push(_v);
      });
      return newValue;
    },
    getObjectByValue(v) {
      for (let i = 0; i < this.items.length; i++) {
        let item = this.items[i];
        if (v === item.value) {
          return {
            text: item.text, value: v
          };
        }
      }
      return void 0;
    },
    // 判断两个值是否相同
    isSame(p1, p2) {
      if (dsf.type(p1) === "array" && dsf.type(p2) === "array") {
        if (p1.length !== p2.length) return false;
        for (let i = 0; i < p1.length; i++) {
          let same = false;
          for (let j = 0; j < p2.length; j++) {
            if (p1[i].text === p2[j].text && p1[i].value === p2[j].value) {
              same = true;
              break;
            }
          }
          if (!same) {
            return false
          }
        }
        return true;
      }
      return p1 === p2;
    },
    updateValue(to) {
      if (this.multiple) {
        this.emitValueChange(to);
        this.$nextTick(() => {
          this.emitFormValidate(to);
        })
      } else {
        const item = to.length ? to[0] : { 'text': '', 'value': '' };
        this.emitValueChange(item);
        this.$nextTick(() => {
          this.emitFormValidate(item);
        })

      }
    }
  },
  design: {
    layout: {
      //#开头表示该属性在预览模式下会被保留
      exclude: ["value", "#items"],
    }
  }
});
</script>
