<template>
  <div v-if="isDesign || (sPID && sCurLinkID)" class="ds-control ds-form-item ds-textbox" :class="getCss">
    <label
      v-if="showLabel"
      class="ds-form-label"
      :style="getLabelWidthStyle()"
    >
      {{ label }}
      <i v-if="dataCapion" class="iconfont icon-bangzhuzhongxin ds-control-helper" @click="showHelper"></i>
    </label>
    <div class="ds-form-block" :style="getFormItemBlockMarginLeft()">
      <template v-if="!readOnly">
        <el-input
          v-model="inputValue"
          :placeholder="placeholder"
          type="text"
          :disabled="disabled"
        />
        <div class="dis-btns">
          <i
            class="iconfont icon-cloud-upload"
            title="上传附件"
            @click="upload"
          ></i>
          <el-popover
            v-model="showOptions"
            placement="right"
            width="360"
            trigger="click"
          >
            <ul class="temp-list">
              <li
                v-for="(option, index) in options"
                :key="index"
                :title="option.key_word"
                class="item"
                @click="setValue(option)"
              >
                {{ option.key_word }}
              </li>
            </ul>
            <i
              slot="reference"
              class="iconfont icon-detail temp-icon"
              title="一键模板"
            ></i>
          </el-popover>
        </div>
        <DsfUploadFile ref="uploadFile" :hide-btns="true" @value-change="getValue"></DsfUploadFile>
      </template>
    </div>
  </div>
</template>
<script>
import formControl from "../../../mixins/formControl";
export default dsf.component({
  name: "DsfDisposeIdea",
  mixins: [formControl],
  ctrlCaption: "处理意见",
  props: {
    caption: {
      type: String,
      default() {
        return "";
      },
    },
    label: {
      type: String,
      default() {
        return "";
      },
    },
    placeholder: {
      type: String,
      default() {
        return "请输入";
      },
    },
    showLabel: {
      type: Boolean,
      default() {
        return true;
      },
    },
    disabled: {
      type: Boolean,
      default() {
        return false;
      },
    },
    visible: {
      type: Boolean,
      default() {
        return true;
      },
    },
    tbNoPadding: {
      type: Boolean,
      default() {
        return false;
      },
    },
    metadata: {
      type: Object,
      default() {
        return {
          id: "",
          name: "处理意见",
          type: {
            value: 3,
            text: "业务元数据"
          },
          valueAttributes: [
            {
              name: "文本",
              code: "text",
              type: dsf.metadata.getDataType("string"),
              length: "50",
              defaultValue: null,
              unit: null,
            },
          ]
        };
      }
    }
  },
  data() {
    return {
      showOptions: false,
      sPID: this.$route.query.sPID,
      sCurLinkID: this.$route.query.sCurLinkID,
      options: [],
      multipleFiles: [],
      isSave: true,
      inputValue: ""
    };
  },
  mounted() {
    // console.log("挂载文本")
    if (!this.isDesign && this.sPID && this.sCurLinkID) {
      this.initOptions();
    }
    if(this.$vm && this.$vm.flows){
      this.$vm.flows.disposeIdea = this;
    }
  },
  updated() {
    // console.log("修改文本")
  },
  destroyed() {
    // console.log("文本框被销毁")
  },
  methods: {
    // 获取角色树
    initOptions() {
      this.$http
        .get("wfr/getOpinionKeyWord", {}, $$webRoot.dsfa)
        .then((res) => {
          if (res.data.success) {
            this.options = res.data.data;
          }
        });
    },
    setValue(option) {
      this.inputValue = option.key_word;
      this.showOptions = false;
    },
    upload() {
      this.$refs.uploadFile.openSelectFile();
    },
    getValue(val) {
      this.multipleFiles = JSON.parse(val.args);
    },
    setDoSave(val) {
      this.isSave = val;
    },
    postBefore(opts){
      if (!this.isSave || !this.sPID && !this.sCurLinkID) {
        // next();
      } else {
        let param = {
          sPID: this.sPID,
          sCurLinkID: this.sCurLinkID,
          sOpinion: {
            'Content': this.inputValue,
            'Attachments': this.multipleFiles || []
          }
        }
        param.sOpinion = JSON.stringify(param.sOpinion);
        this.$http.get('wfr/opinion', param)
          .then(res => {
            if (res.data.success) {
              next();
            } else {
              dsf.layer.message(res.data.message || (this.label || '处理意见') + '异常', false);
            }
          })
          .catch(res => {
            dsf.layer.message((this.label || '处理意见') + '异常', false);
          });
      }
    },
  },
});
</script>
