<template>
  <div class="ds-control ds-section" :class="getCss">
    <iframe :src="iframeUrl" width="100%" height="100%" frameborder="0">
    </iframe>
  </div>
</template>
<script>
import control from "_platform/dsf/mixins/control";
export default dsf.component({
  name: "DsfIframe",
  ctrlCaption: "IFrame",
  mixins: [control],
  data() {
    return {
      iframeUrl: "",
    };
  },
  props: {
    rmiframeUrl: {
      type: String,
      default: "",
    },
    $lazyLoad: {
      type: Boolean,
      default: false,
    },
  },
  computed: {
    getCss() {
      return [
        ...this.$getClass(),
        this.showBorder ? "" : "ds-no-border",
        this.noPadding ? "" : "ds-no-padding",
      ];
    },
  },
  created() {
    if (!this._isRender && !this.isDesign) {
      if (!this.$lazyLoad) {
        this.loadView();
      }
    }
  },
  methods: {
    loadView() {
      let v = this.$replace(this.rmiframeUrl);
      v = dsf.url.getWebPath(v);
      v = this.analysis(v);
      this.iframeUrl = v;
    },
    analysis(url) {
      var searchStr = url.substr(url.indexOf("?"));
      var oRegex = /[\?&](\w+)=([^&]+)?/gi;
      url = url.replace(oRegex, function (a, b, c, d) {
        var s = a.substr(0, 1);
        s += b + "=" + encodeURI(c || "");
        return s;
      });
      return url;
    },
  },
});
</script>
