<!-- 问卷 -->
<template>
  <div v-show="showCtrl" class="ds-control ds-question-item ds-radio" :class="{'ds-qi-has-rela':!hasRela,'ds_hide':!visible}">
    <DsfQITitle :required="required" :question-index="questionIndex" :question-body="questionBody"></DsfQITitle>
    <el-radio-group v-if="layout == 0" v-model="radioValue" :disabled="readOnly" @change="change">
      <el-radio v-for="item in items" :key="item.value" :label="item.value">{{ item.text }}</el-radio>
      <el-radio v-if="isOther && !hasOther" label="else">
        其他
      </el-radio>
    </el-radio-group>
    <el-radio-group v-else v-model="radioValue" :disabled="readOnly" @change="change">
      <el-row>
        <el-col v-for="item in items" :key="item.value" :span="getColSpan">
          <el-radio :label="item.value">{{ item.text }}</el-radio>
        </el-col>
        <el-col v-if="isOther && !hasOther" :span="getColSpan">
          <el-radio label="else">
            其他
          </el-radio>
        </el-col>
      </el-row>
    </el-radio-group>
  </div>
</template>

<script>
import questionControl from "_platform/dsf/mixins/questionControl";

export default dsf.component({
  name:'DsfQIRadio',
  ctrlCaption: "单选题",
  mixins: [questionControl],
  data () {
    return {
    }
  },

  props:{
    items:{
      type:Array,
      default() {
        return [{text:'选项1',value:1},{text:'选项2',value:2}]
      }
    },
    metadata: {
      type: Object,
      default() {
        return dsf.metadata.get("items-meta-data");
      }
    },
    // 是否其他
    isOther: {
      type: Boolean,
      default: false,
    },
    // 排列样式
    layout:{
      type:[String,Number],
      default: 0
    }
  },

  components: {},

  computed: {
     // 判断items中是否已经存在其他
    hasOther() {
      return this.items.findIndex((v) => v.value === "else") > -1;
    },
    radioValue: {
      get() {
        return this.value ? this.value.value : '';
      },
      set(v) {
        let selected = _.find(this.items || [], (item) => {
          return item.value === v;
        });
        let newValue = selected ? selected : { value:v,text:'其他' };
        this.emitValueChange(newValue);
        this.emitFormValidate(newValue);
      },
    },
    getColSpan() {
      switch(this.layout.toString()) {
        case '1':
          return 24;
        case '2':
          return 12;
        case '3':
          return 8;
        case '4':
          return 6;
        case '5':
          return 4;
        default:
          return 24;
      }
    },
  },

  watch: {
    // 监听值
    value(v) {
      if (
        dsf.type(v) === "string" ||
        dsf.type(v) === "number" ||
        dsf.type(v) === "boolean"
      ) {
        let item = _.find(this.items, (item) => {
          return item.value === v;
        });
        this.emitValueChange(item ? item : { value:v,text:'其他' });
      }
    },
  },

  mounted() {},

  created() {}, 

  methods:{
    change(val){
      console.log(val)
    }
  },

  design: {
    layout: {
      //#开头表示该属性在预览模式下会被保留
      exclude: ["value", "#items"],
    }
  }
})

</script>