import { flow } from "lodash";

export default {
  //表单保存
  system_form_save(args) {
    let form = args._self.$form;
    if (form) {
      let options = {
        isRefresh: args.params[0] === true ? true : false,
        savedClose: args.params[2] === false ? false : true,
        successMsg: args.params[3]||"保存成功",
        errorMsg: args.params[4]||"保存失败"
      }
      form.formSave(options);
    }
  },
  //打开新窗口
  system_open_window(args) {
    let url = args.params[0];
    if (url) {
      args._self.$openWindow(url);
    }
  },
  //关闭窗口（对话框）
  system_close_window(args) {
    if (args._self.$vm) {
      let dialog = args._self.dialog;
      if (dialog) {
        dialog.dialogVisible = false;
      } else {
        if (window.opener) {
          window.close();
        }
      }
    }
  },
  //批量删除
  system_datagrid_batch_delete(args) {
    let rows = args.owner.multipleSelection;
    if (rows.length === 0) {
      dsf.layer.message("请选择至少一条数据", false);
      return;
    }
    dsf.layer
      .confirm("是否要删除选中的" + rows.length + "条数据？")
      .then((result) => {
        let data = args.data;
        if (data) {
          let params = args.params;
          let key = params[0] || "_id";
          let isTree = params[1] === "true";
          let ids = _.map(args.owner.multipleSelection, (row) => {
            return row[key];
          });
          let post = {
            namespace: args.owner.$vm.nameSpace,
            pageName: args.owner.$vm.pageName,
            isTree: isTree,
            data: JSON.stringify(ids),
          };
          args._self.$http
            .post("/meta/list/delete", post)
            .then(({ data }) => {
              dsf.layer.message("删除成功", true);
              args.owner.clearSelect();
              args.owner.$nextTick(function () {
                args.owner.loadData();
              });
            })
            .catch((ex) => {
              dsf.layer.message("删除失败", false);
            });
        }
      })
      .catch((ex) => {
        throw ex;
      });
  },
  //列表行删除数据
  system_datagrid_delete(args) {
    dsf.layer
      .confirm("是否要删除该数据")
      .then((result) => {
        let data = args.data;
        if (data) {
          let params = args.params;
          let key = params[0] || "_id";
          let isTree = params[1] === "true";
          let ids = [data[key]];
          let post = {
            namespace: args.owner.$vm.nameSpace,
            pageName: args.owner.$vm.pageName,
            isTree: isTree,
            data: JSON.stringify(ids),
          };
          args._self.$http
            .post("/meta/list/delete", post)
            .then(({ data }) => {
              dsf.layer.message("删除成功", true);
              if(args.owner){
                args.owner.toggleSelect(args.data, false);
                args.owner.$nextTick(function () {
                  args.owner.loadData();
                });
              }
            })
            .catch((ex) => {
              dsf.layer.message("删除失败", false);
            });
        }
      })
      .catch((ex) => {
        throw ex;
      });
  },
  // 子表插入行
  system_subtable_insert(args, $event) {
    let row = {};
    args.owner.value.push(row);
    args.owner.$nextTick(() => {
      args.owner.editBegin(args.owner.value[args.owner.value.length - 1]);
    });
    //取消点击事件冒泡，防止出发子表结束编辑事件
    $event.stopPropagation();
  },
  // 子表清空
  system_subtable_clear(args) {
    dsf.layer.confirm("是否要清空当前所有行？")
      .then(() => {
        args.owner.clearRows();
      }).catch(() => {})
  },
  // 子表选择
  system_subtable_datachoice(args) {
    let wh = args.params[2] ? args.params[2].split('/') : ['1000px', '70vh']
    args._self.$openDialog({
      title: args.params[1] || '数据选择',
      width: wh[0],
      height: wh[1],
      params: {
        path: args.params[0],
      },
      btns: [{
          text: "确定",
          handler: (res) => {
            const data = res.yes();
            if (data === false) {
              return false;
            }
            args.owner.$dispatch('handleTreeInfo', data)
          },
        },
        {
          text: "取消",
        },
      ],
    })
  },
  // 问卷保存
  system_qi_save(evt) {
    let form = evt._self.$form;
    if (form) {
      form.formSave();
    }
  },
  async system_flow_send(args) {
    let disposeIdea;
    if(args._self && args._self.$vm && args._self.$vm.flows && args._self.$vm.flows.disposeIdea) {
      disposeIdea = args._self.$vm.flows.disposeIdea;
      disposeIdea.setDoSave(false);
    }
    let form = args._self.$form;
    if (form) {
      let options = {
        isSilent: true,
        isRefresh: false,
        savedClose: false,
        successMsg: "",
        errorMsg: ""
      }
      const res = await form.formSave(options);
      if(res) {
        this.$refs.wfinfo && this.$refs.wfinfo.flowSend(res);
      }
      if(disposeIdea) {
        disposeIdea.setDoSave(true);
      }
    }
  },
  system_flow_sendBack(args) {
    this.$refs.wfinfo && this.$refs.wfinfo.getSendBackLink(args.params);
  }
};