<script>
import control from '_platform/dsf/mixins/control'
import DsfOneThingBox from './DsfOneThingBox'
export default dsf.component({
  ctrlCaption: '应用中心组件',
  mixins: [control],
  name: "DsfOneThingApplication",
  components: {
    DsfOneThingBox
  },
  methods: {
    sceneanditems() {
      this.$openDialog({
        title: "并联办事信息",
        width: '935px',
        height: '510px',
        params: {
          path: "ot/otherpage/sceneanditems"
        },
        btns: [
          {
            text: "确定",
            handler: (res) => {
              const data = res.yes();
              if (data === false) {
                return false;
              }
              window.setTimeout(() => {
                this.$openWindow({
                  dialogArgs: data,
                  url: './page.html#/pc/ot/fillin/form'
                })
              }, 500)
            },
          },
          {
            text: "取消",
          },
        ],
      })
    },
    eventJump(url) {
      window.open(url, '_self');
    }

  }
});
