<template>
  <div class="ds-control ds-form-item ds-date-picker" :class="getCss">
    <template v-if="simple && readOnly && !isDesign">
      <span :title="getShowContent() | proxy(formatter)">{{ getShowContent() | proxy(formatter) }}</span>
      <div v-if="errors.length" class="ds-error-text">{{ errorsMsg }}</div>
    </template>
    <template v-else>
      <label v-if="showLabel" class="ds-form-label" :style="getLabelWidthStyle()">
        {{ label }}
        <i v-if="dataCapion" class="iconfont icon-bangzhuzhongxin ds-control-helper" @click="showHelper"></i>
      </label>
      <div class="ds-form-block" :style="getFormItemBlockMarginLeft()">
        <el-date-picker
          v-if="!readOnly"
          ref="datePicker"
          v-model="value2"
          :disabled="disabled"
          :type="chooseType"
          :editable="isInput"
          :format="timeFormat"
          :clearable="clearable"
          :placeholder="placeholder"
          :picker-options="pickerOptions"
          :value-format="valueFormat"
          @focus="pickerShow"
        />
        <!--只读状态只显示div-->
        <div v-else-if="readOnly" class="ds-form-readonly">
          <span :title="getShowContent() | proxy(formatter)">{{ getShowContent() | proxy(formatter) }}</span>
        </div>
        <div v-if="errors.length" class="ds-error-text">{{ errorsMsg }}</div>
      </div>
    </template>
  </div>
</template>

<script>
import formControl from "../../../mixins/formControl";

export default dsf.component({
  inheritAttrs: false,
  name: "DsfDatePicker",
  mixins: [formControl],
  ctrlCaption: "日期",
  props: {
    simple: {
      type: Boolean,
      default: false
    },
    placeholder: {
      type: String,
      default: ""
    },
    value: {
      type: String,
      default: ""
    },
    label: {
      type: String,
      default: "日期选择"
    },
    // 时间显示格式
    $timeFormat: {
      type: String,
      default: "yyyy-MM-dd"
    },
    // 日期选择器类型
    $chooseType: {
      type: String,
      default: "date"
    },
    // 是否显示清除按钮
    clearable: {
      type: Boolean,
      default: true
    },
    // 是否以当前日期为起始日期
    // startIsNow: {
    //   type: Boolean,
    //   default: false
    // },
    // // 是否以当前日期为结束日期
    // endIsNow: {
    //   type: Boolean,
    //   default: false
    // },
    // 是否可输入日期
    isInput: {
      type: Boolean,
      default: false
    },
    // 显示小时段
    $timeInter: {
      type: Array,
      default () {
        return [
          '00:00:00',
          '23:59:59'
        ]
      }
    },
    // 时间选择步骤
    // 默认直接选择日
    pickerStepType: {
      type: Number,
      default: 0
    },
    // 弹框初始时间
    pickerInitDate: {
      type: Number,
      default: null
    },

    /* 可选范围设置 */
    selectStartType:{
      type: Number,
      default: 0
    },
    selectEndType:{
      type: Number,
      default: 0
    },
    // 固定日期
    selectStartDate: {
      type: Number,
      default: Date.now()
    },
    selectEndDate: {
      type: Number,
      default: Date.now()
    },
    // 动态日期(根据天数)
    selectStartDateByDays: {
      type: Number,
      default: 0
    },
    selectEndDateByDays: {
      type: Number,
      default: 0
    },
    // 动态日期(根据月数)
    selectStartDateByMonths: {
      type: Number,
      default: 0
    },
    selectEndDateByMonths: {
      type: Number,
      default: 0
    },
    // 动态日期(根据年数)
    selectStartDateByYears: {
      type: Number,
      default: 0
    },
    selectEndDateByYears: {
      type: Number,
      default: 0
    },

    metadata: {
      type: Object,
      default() {
        return dsf.metadata.get("date-picker-meta-data");
      }
    }
  },
  data() {
    return {
      value2: ''
    }
  },
  watch: {
    'value' (to) {
      this.initValue(to);
    },
    'value2' (to) {
      this.emitValueChange(to);
      this.emitFormValidate(to);
    }
  },
  computed: {
    chooseType () {
      return this.$chooseType === 'datetime_s' ? 'datetime' : this.$chooseType;
    },
    timeFormat () {
      if (this.$chooseType === 'datetime') {
        return this.$timeFormat + ' HH:mm';
      }
      if (this.$chooseType === 'datetime_s') {
        return this.$timeFormat + ' HH:mm:ss';
      }
      return this.$timeFormat;
    },
    valueFormat () {
      if (this.$chooseType === 'datetime') {
        return 'yyyy-MM-dd HH:mm';
      }
      if (this.$chooseType === 'datetime_s') {
        return 'yyyy-MM-dd HH:mm:ss';
      }
      return 'yyyy-MM-dd'
    },
    pickerOptions () {
      return {
        selectableRange: this.$timeInter[0] + '-' + this.$timeInter[1],
        // 限制日期选择范围
        disabledDate: d => {
          d = new Date(dsf.date.format(d, 'yyyy/mm/dd 00:00:00'));
          return this.pickerStartTime(d) || this.pickerEndTime(d);
        }
      }
    }
  },
  created() {
    this.initValue(this.value);
  },
  methods: {
    initValue(v) {
      if (v !== null && v !== undefined && v !== '' && v !== this.value2) {
        this.value2 = v;
      }
    },
    // this.picker为当前时间控件的弹框
    // picker只有在控件第一次获得焦点时，才会被实例化
    pickerShow() {
      if (!this.picker) {
        this.$nextTick(() => {
          this.picker = this.$refs.datePicker.picker;
          if (this.pickerInitDate !== null) {
            this.picker.date = new Date(this.pickerInitDate);
          }
          this.pickerStepType && this.initPicker();
        });
      } else if (this.pickerStepType) {
        this.$nextTick(() => {
          this.initPicker();
        });
      }
    },
    // 控制弹框选择时间的步骤
    initPicker() {
      this.picker.currentView = 'year';
    },

    getDate(date, format = 'yyyy/mm/dd 00:00:00') {
      if (date === undefined) {
        date = new Date();
      }
      return new Date(dsf.date.format(date, format))
    },
    // 计算时间范围--起始
    pickerStartTime(d) {
      if (!this.selectStartType) return false;
      // 当前日期
      if (this.selectStartType === 1) {
        const now = this.getDate();
        return d.getTime() < now.getTime();
      }
      // 固定日期
      if (this.selectStartType === 2) {
        const _d = this.getDate(this.selectStartDate);
        return d.getTime() < _d.getTime();
      }
      // 动态日期(根据天数)
      if (this.selectStartType === 3) {
        let _d = this.getDate();
        _d = this.getDate(_d.getTime() + this.selectStartDateByDays * 86400000);
        return d.getTime() < _d.getTime();
      }
      // 动态日期(根据月数)
      if (this.selectStartType === 4) {
        let _d = new Date();
        let m = _d.getMonth() + this.selectStartDateByMonths + 1;
        let y = _d.getFullYear() + Math.floor(m / 12) + '/';
        m = m % 12;
        m = m.length === 1 ? '0' + m : m;
        _d = new Date(y + m + '/01 00:00:00');
        return d.getTime() < _d.getTime();
      }
      // 动态日期(根据年数)
      if (this.selectStartType === 5) {
        let _d = new Date();
        let y = _d.getFullYear() + this.selectStartDateByYears;
        _d = new Date(y + '/01/01 00:00:00');
        return d.getTime() < _d.getTime();
      }
      return false;
    },
    // 计算时间范围--结束
    pickerEndTime(d) {
      if (!this.selectEndType) return false;
      // 当前日期
      if (this.selectEndType === 1) {
        const now = this.getDate();
        return d.getTime() > now.getTime();
      }
      // 固定日期
      if (this.selectEndType === 2) {
        const _d = this.getDate(this.selectEndDate);
        return d.getTime() > _d.getTime();
      }
      // 动态日期(根据天数)
      if (this.selectEndType === 3) {
        let _d = this.getDate();
        _d = this.getDate(_d.getTime() + this.selectEndDateByDays * 86400000);
        return d.getTime() > _d.getTime();
      }
      // 动态日期(根据月数)
      if (this.selectEndType === 4) {
        let _d = new Date();
        let m = _d.getMonth() + this.selectEndDateByMonths + 2;
        let y = _d.getFullYear() + Math.floor(m / 12) + '/';
        m = m % 12;
        m = m.length === 1 ? '0' + m : m;
        _d = new Date(y + m + '/01 00:00:00');
        _d.setTime(_d.getTime() - 86400000);
        return d.getTime() > _d.getTime();
      }
      // 动态日期(根据年数)
      if (this.selectEndType === 5) {
        let _d = new Date();
        let y = _d.getFullYear() + this.selectEndDateByYears;
        _d = new Date(y + '/12/31 00:00:00');
        return d.getTime() > _d.getTime();
      }
      return false;
    }
  }
});
</script>