<script>
import layout from "../../../mixins/layout";
// import DsfSubTableCellContent from './subTableCellContent';
export default dsf.component({
  name: "DsfSubTableColumn",
  inheritAttrs: false,
  mixins: [layout],
  ctrlCaption: "子表列",
  props: {
    column: {
      type: Object,
      default() {
        return {
          label: "",
          children: []
        };
      }
    },
    columns: {
      type: Array,
      default() {
        return [];
      }
    }
  },
  data() {
    return {};
  },
  created() { },
  computed: {
    ownerTable() {
      let parent = this.$parent;
      while (parent.$options.name != "DsfSubTable") {
        parent = parent.$parent;
      }
      return parent;
    }
  },
  beforeMount() { },
  mounted() { },
  methods: {},
  // updated(){
  //   console.log('1')
  // },
  render(h) {
    // let table=this.ownerTable;
    return renderHeader.call(this, h, this.column, this.$vnode.data.key);
  }
});

function renderHeader(h, column, key) {
  let _this = this;
  let child = [];
  let scopedSlots = null;
  if (column.children && column.children.length > 0) {
    child = _.map(column.children, (c, i) => {
      return renderHeader.call(this, h, c, i);
    });
  } else {
    child = this.$slots[column.columnId];
    scopedSlots = _this._u([
      {
        key: "default",
        fn(scope) {
          return [_this._t(column.columnId, null, null, scope)];
        }
      }], null, true);
  }
  let data = {
    key: key,
    props: column
  };
  if (scopedSlots) {
    data.scopedSlots = scopedSlots;
  }
  if (this.ownerTable) {
    if (this.ownerTable.isDesign) {
      let vnode = h("el-table-column", data, child);
      return vnode;
    } else {
      if (!column.hide) {
        let vnode = h("el-table-column", data, child);
        return vnode;
      }
    }
  }
  return null;
}
</script>
