<template>
  <li class="ds-aside-menu-item" :class="{active: isActive, show: showChildren}">
    <div
      :style="{'padding-left': level * 20 + 'px'}"
      class="ds-aside-menu-item-box"
      @click="clickEvent"
    >
      <i v-if="level === 1" :class="[menu.menu_icon]" class="ds-aside-menu-item-icon iconfont" />
      <span class="ds-aside-menu-item-name">{{ menu.menu_name }}</span>
      <i
        v-if="menu.children"
        :class="[{show: showChildren}, showChildren ? 'icon-xia1' : 'icon-zuo' ]"
        class="iconfont ds-aside-menu-item-show"
      />
    </div>
    <div v-if="menu.children" v-show="showChildren">
      <dsf-menu :menu-list="menu.children" :level="level + 1"></dsf-menu>
    </div>
  </li>
</template>
<script>
export default {
  name: 'DsfMenuItem',
  inject: ['root', 'aside'],
  props: {
    menu: {
      type: Object,
      required: true
    },
    level: {
      type: Number,
      required: true
    },
    index: {
      type: Number,
      required: true
    }
  },
  data() {
    return {
      isActive: false,
      showChildren: false,
      parent: {},
    }
  },
  watch: {
  },
  created() {
  },
  mounted() {
    this.parent = this.$parent;
  },
  methods: {
    clickEvent () {
      if (this.menu.children) {
        this.showChildren = !this.showChildren;
        if (this.showChildren) {
          this.aside.setShow(this, this.level);
        } else {
          this.aside.setShow(null, this.level);
          this.showChildren = false;
        }
      } else {
        this.linkTo(this.menu);
      }
    },
    linkTo(item) {
      let path = '';
      if (item.menu_url) {
        path = item.menu_url;
      } else if (item.children && item.children.length) {
        path = item.children[0].menu_url;
      }
      if (path) {
        window.location.href = path;
      }
    }
  }
}
</script>