<!-- 问卷 -->
<template>
  <div v-show="showCtrl" class="ds-control ds-question-item ds-question-rate" :class="{'ds-qi-has-rela':!hasRela,'ds_hide':!visible}">
    <p :class="['ds-question-body',{'ds-question-require':required}]"><span class="ds-question-index">{{ questionIndex }}：</span>{{ questionBody }}</p>
    <el-rate
      v-model="inputValue"
      :max="+starLength"
      :allow-half="half"
      :show-text="showText"
      :texts="textsArr"
      :disabled="readOnly"
    ></el-rate>
  </div>
</template>

<script>
import questionControl from "_platform/dsf/mixins/questionControl";

export default dsf.component({
  name:'DsfQIRate',
  ctrlCaption: "评分题",
  mixins: [questionControl],
  data () {
    return {
      inputValue:0,
    };
  },

  props:{
    starLength:{
      type:[Number,String],
      default:5
    },
    half:{
      type:Boolean,
      default:false
    },
    showText:{
      type:Boolean,
      default:false
    },
    texts:{
      type:String,
      default:"极差,失望,一般,满意,惊喜"
    }
  },

  components: {},

  computed: {
    textsArr:function() {
      return this.texts.split(',')
    }
  },

  mounted() {},

  created() {},

  methods: {}
})

</script>