import DsfHome from './home';
import DsfHomeHeader from './homeHeader';
import DsfHomeAside from './homeAside';
import DsfMenu from './aside/menu';
import DsfMenuItem from './aside/menuItem';

export {
  DsfHome,
  DsfHomeHeader,
  DsfHomeAside
};

export default {
  install (Vue) {
    Vue.component(DsfMenu.name, DsfMenu);
    Vue.component(DsfMenuItem.name, DsfMenuItem);
    Vue.component(DsfHome.name, DsfHome);
    Vue.component(DsfHomeHeader.name, DsfHomeHeader);
    Vue.component(DsfHomeAside.name, DsfHomeAside);
  }
};