      <div class="ds-one-thing-title-line"></div>{{ title }}<slot name="title"></slot>
    </div>
    <div v-if="isGo" class="icon-box">
      <i class="iconfont icon-weibiaoti34" @click="go"></i>
    </div>
    <div>
      <slot name="content"></slot>
    </div>
  </div>
</template>

<script>
export default {
   props: {
    // 标题
    title: {
      type: String,
      default: ''
    },
    // 跳转路由
    link: {
      type:String,
      default: ''
    },
    // 是否显示
    isGo: {
      type:Boolean,
      default: true
    }
  },

  data () {
    return {
    };
  },

  computed: {},

  mounted() {},

  created() {},

  methods: {
    go(){
      if(this.link != ''){
        this.$router.push({path:this.link});
      }
    }
  }
}

</script>
