<template>
  <div class="ds-super-search">
    <div v-if="showFitler" class="ds-super-search-row el-row">
      <div class="ds-super-search-item el-col-24">
        <label class="ds-super-search-item-text">筛选:</label>
      </div>
    </div>
    <div v-for="(cells,$index) in layout" :key="$index" class="ds-super-search-row el-row" :style="{'padding':'2.5px 0px'}">
      <!-- <div class="el-col-"></div> -->
      <div v-for="(item) in cells" :key="item.id" :item-key="item.id" :class="['el-col-'+(item.layout),(item.showModel=='tile'?'tile':'')]" class="ds-super-search-item">
        <label class="ds-super-search-item-text">{{ item.label }}:</label>
        <div class="ds-super-search-item-block" :class="{'open':item.isExtend}">
          <template v-if="item.type=='enum'">
            <div v-if="item.showModel=='tile'" class="ds-super-search-item-option">
              <div class="ds-super-search-item-option-box">
                <el-checkbox-group v-model="item.values">
                  <el-checkbox v-for="op in (optionsMap[item.optionKey]||[])" :key="op.value" :label="op.value" @change="checkboxChange($event,item,op)">{{ op.text }}</el-checkbox>
                </el-checkbox-group>
              </div>
            </div>
            <div v-else-if="item.showModel=='select'">
              <el-select v-model="item.values" class="indiction-select" collapse-tags style="width:100%;line-height:40px;" filterable multiple placeholder="请选择">
                <el-option v-for="op in (optionsMap[item.optionKey]||[])" :key="op.value" :label="op.text" :value="op.value">
                </el-option>
              </el-select>
            </div>
          </template>
          <template v-if="item.type=='text'">
            <el-input v-model="item.values" @change="textChange(item)"></el-input>
          </template>

          <template v-if="item.type=='datetime'">
            <el-date-picker v-model="item.values" style="width:100%" type="daterange" range-separator="至" start-placeholder="开始日期" end-placeholder="结束日期" @change="dateChange(item)">
            </el-date-picker>
            <!-- <el-date-picker v-model="item.values" style="width:100%" align="left" type="date" placeholder="选择日期">
            </el-date-picker> -->
          </template>
        </div>
        <div v-if="item.showModel=='tile'" class="ds-super-search-item-buttons">
          <template v-if="item.isMore">
            <dsf-button v-if="!item.isExtend" style="min-width:auto" type="auxiliary" size="small" icon="shuangjiantouxia" @click="extend(item,true)">
              更多
            </dsf-button>
            <dsf-button v-else style="min-width:auto" type="auxiliary" size="small" icon="shuangjiantoushang" @click="extend(item,false)">
              收起
            </dsf-button>
          </template>
        </div>
      </div>
    </div>
  </div>
</template>
<script>

export default {
  name: 'DsfSuperSearch',
  inheritAttrs: false,
  props: {
    owner: {
      type: Object,
      default() {
        return null;
      }
    },
    searchItemSetting: {
      type: Array,
      default() {
        return []
      }
    },
    searchHeight: {
      type: Number,
      default: 0
    },
    defaultRowsNumber: {
      type: Number,
      default: 2
    },
    showFitler: {
      type: Boolean,
      default: false
    },
    value: {
      type: Object,
      default() {
        return {};
      }
    }
  },
  data() {
    return {
      hasExtend: false,
      searchItems: [],
      optionsMap: {},
      totalRowsNumber: 0,
      layout: []
    }
  },
  watch: {
    searchItemSetting: {
      handler(v) {
        this.updateSearchItemSetting();
      },
      immediate: true
    },
    showFitler: {
      handler(v) {
        this.$nextTick(() => {
          this.emitHeight();
        });
      }
    }
  },
  created() {
    window.addEventListener("resize", this.resize);
  },
  async mounted() {
    // this.optionsMap = await this.updateData();
  },
  beforeDestroy() {
    window.removeEventListener('resize', this.resize)
  },
  methods: {
    async updateSearchItemSetting() {
      this.totalRowsNumber = this.getTotalRowsNumber();
      let rows = this.totalRowsNumber < this.defaultRowsNumber ? this.totalRowsNumber : this.defaultRowsNumber;
      if (this.totalRowsNumber > this.defaultRowsNumber) {
        this.hasExtend = true;
      }
      this.emitInitHeight(rows);
      this.searchItems = _.map(this.searchItemSetting, (el) => {
        return {
          id: el.id,
          field: el.field,
          label: el.fieldCaption,
          valueSource: el.setting,
          type: el.searchType,
          optionKey: el.id || "",
          values: el.searchType == "enum" ? [] : '',
          showModel: el.model,
          multiple: false,
          isExtend: false,
          isMore: false,
          layout: el.layout || 24,
          fields: _.filter(el.children, (c) => c.join == 1)
        }
      });
      // console.log(this.searchItems)
      this.optionsMap = await this.updateData();
      console.log(this.optionsMap)
      this.setLayout();
      this.$nextTick(() => {
        this.resize();
      })
    },
    getTotalRowsNumber() {
      let total = 0;
      _.each(this.searchItemSetting, (el) => {
        total += ~~el.layout;
      });
      let row_num = Math.ceil(total / 24);
      return row_num;
    },
    setLayout() {
      let matrix = [];
      let count = 0;
      let rowNum = 0;
      _.each(this.searchItems, (el, i) => {
        if (!matrix[rowNum]) {
          matrix[rowNum] = [];
        }
        if (count < 24) {
          matrix[rowNum].push(el);
          count += ~~el.layout;
        }
        let next = i + 1;
        if (this.searchItems[next]) {
          let temp = count + ~~this.searchItems[next].layout;
          if (temp > 24) {
            rowNum++;
            count = 0;
          }
        }
      });
      this.layout = matrix;
    },
    async updateData() {
      let optionsMap = {};
      let optionsRequests = [];
      let dictArr = [];
      _.each(this.searchItems, (v) => {
        if (v.type == "enum") {

          if (v.valueSource?.type == "dict") {
            let params = {
              "key": v.id,
              "dictId": v.valueSource.value.key
            }
            dictArr.push(params)
          }
          else if (v.valueSource?.type == "dbsource") {
            let path = v.valueSource.value.key.replace(/\./g, "/");
            optionsRequests.push({
              id: v.id,
              request: this.$webAPI.dbSourceInterface(path, (this.owner?.queryString || {}))
            });
          }
          else if (v.valueSource?.type == "interface") {
            let http = this.$http.get(v.valueSource.value, (this.owner?.queryString || {}));
            optionsRequests.push({
              id: v.id,
              request: http
            });
          }
          else {
            if (v.valueSource && v.valueSource.value) {
              optionsMap[v.id] = v.valueSource.value;
            }
            else {
              optionsMap[v.id] = []
            }
          }
        }
      });
      if (dictArr.length > 0) {
        let params = {
          dicts: dictArr
        }
        optionsRequests.push({
          id: 'dicts',
          request: this.$webAPI.getBatchDict(params)
        })
      }
      try {
        if (optionsRequests.length) {
          let result = await this.$http.all(_.map(optionsRequests, 'request'));
          _.each(result, (res, i) => {
            if (optionsRequests[i].id == 'dicts') {
              for (let k in res.data) {
                optionsMap[k] = res.data[k];
              }
            }
            else {
              optionsMap[k] = res.data;
            }
          });
        }
        return optionsMap;
      }
      catch (err) {
        // dsf.layer.message("查询检索选项出现错误", false);
        dsf.error('查询检索选项出现错误')
      }
    },
    checkboxChange(v, item, option) {
      if (!item.multiple) {
        // if (v) {
        //   item.values = [option.value];
        // } else {
        //   item.values = [];
        // }

      }
      this.postSearch()
    },
    textChange() {
      this.postSearch();
    },
    dateChange() {

      this.postSearch();
    },
    extend(item, mark) {
      item.isExtend = mark;
      this.$nextTick(() => {
        this.emitHeight();
      })
    },
    postSearch() {
      let value = {};
      _.each(this.searchItems, (el) => {
        if (el.type == "datetime") {
          let arr = dsf.type(el.values) == 'array' ? el.values : [el.values]
          value[el.field] = _.map(arr, (v) => {
            return dsf.date.format(v, 'yyyy-mm-dd');
          });
        }
        else {
          value[el.field] = el.values;
        }

      })
      console.log("提交了查询值")
      this.$emit("update:value", value);
    },
    emitHeight() {
      let height = this.$el.offsetHeight;
      this.$emit("update:searchHeight", height);
    },
    emitInitHeight(rows) {
      let height = rows * 50 + (this.showFitler ? 50 : 0);
      this.$emit("update:searchHeight", height);
    },
    resize() {
      _.each(this.searchItems, (el) => {
        let elm = this.$el.querySelector("[item-key='" + el.id + "']");
        if (elm) {
          if (!el.isExtend) {
            let block = elm.querySelector(".ds-super-search-item-block");
            let optionbox = elm.querySelector(".ds-super-search-item-option");
            if (block && optionbox) {
              let blockHeight = block.offsetHeight;
              let optionHeight = optionbox.offsetHeight;
              if (optionHeight > blockHeight + 10) {
                el.isMore = true;
              }
              else {
                el.isMore = false;
              }
            }
          }
        }

      });
      this.emitHeight();
    }
  }
}
</script>