<template>
  <div class="ds-control ds-form-item ds-date-picker" :class="getCss">
    <template v-if="simple && readOnly && !isDesign">
      <span :title="getShowContent() | proxy(formatter)">{{ getShowContent() | proxy(formatter) }}</span>
      <div v-if="errors.length" class="ds-error-text">{{ errorsMsg }}</div>
    </template>
    <template v-else>
      <label v-if="showLabel" class="ds-form-label" :style="getLabelWidthStyle()">
        {{ label }}
        <i v-if="dataCapion" class="iconfont icon-bangzhuzhongxin ds-control-helper" @click="showHelper"></i>
      </label>
      <div class="ds-form-block" :style="getFormItemBlockMarginLeft()">
        <el-date-picker
          v-if="!readOnly"
          v-model="value2"
          :disabled="disabled"
          :type="chooseType"
          :editable="false"
          :format="timeFormat"
          :clearable="clearable"
          :placeholder="placeholder"
          :value-format="valueFormat"
        />
        <!--只读状态只显示div-->
        <div v-else-if="readOnly" class="ds-form-readonly">
          <span :title="getShowContent() | proxy(formatter)">{{ getShowContent() | proxy(formatter) }}</span>
        </div>
        <div v-if="errors.length" class="ds-error-text">{{ errorsMsg }}</div>
      </div>
    </template>
  </div>
</template>

<script>
import formControl from "../../../mixins/formControl";

export default dsf.component({
  name: "DsfDateIntervalPicker",
  mixins: [formControl],
  ctrlCaption: "日期范围",
  props: {
    simple: {
      type: Boolean,
      default: false
    },
    placeholder: {
      type: String,
      default: ""
    },
    value: {
      type: Object,
      default () {
        return {
          sdate: '',
          edate: ''
        }
      }
    },
    label: {
      type: String,
      default: "日期范围"
    },
    // 时间显示格式
    $timeFormat: {
      type: String,
      default: "yyyy-MM-dd"
    },
    // 日期选择器类型
    $chooseType: {
      type: String,
      default: "daterange"
    },
    // 是否显示清除按钮
    clearable: {
      type: Boolean,
      default: true
    },
    // 显示小时段
    $timeInter: {
      type: Array,
      default () {
        return [
          '00:00:00',
          '23:59:59'
        ]
      }
    },
    metadata: {
      type: Object,
      default() {
        return dsf.metadata.get("date-interval-meta-data");
      }
    }
  },
  data() {
    return {
      value2: ['', '']
    }
  },
  watch: {
    'value' (to) {
      this.initValue(to);
    },
    'value2' (to) {
      const res = {
        sdate: '',
        edate: ''
      }
      if (to.length === 2) {
        res.sdate = to[0];
        res.edate = to[1];
      }
      this.emitValueChange(res);
      this.emitFormValidate(res);
    }
  },
  computed: {
    chooseType () {
      return this.$chooseType === 'datetimerange_s' ? 'datetimerange' : this.$chooseType;
    },
    timeFormat () {
      if (this.$chooseType === 'datetimerange') {
        return this.$timeFormat + ' HH:mm';
      }
      if (this.$chooseType === 'datetimerange_s') {
        return this.$timeFormat + ' HH:mm:ss';
      }
      return this.$timeFormat;
    },
    valueFormat () {
      if (this.$chooseType === 'datetimerange') {
        return 'yyyy-MM-dd HH:mm';
      }
      if (this.$chooseType === 'datetimerange_s') {
        return 'yyyy-MM-dd HH:mm:ss';
      }
      return 'yyyy-MM-dd'
    }
  },
  created() {
    this.initValue(this.value);
  },
  methods: {
    initValue (v) {
      if (v && v.sdate && v.edate && (this.value2[0] !== v.sdate || this.value2[1] !== v.edate)) {
        this.value2 = [v.sdate, v.edate];
      }
    }
  }
});
</script>