<template>
  <div
    v-loading="loading"
    class="ds-control ds-form-item ds-upload"
    :class="getCss"
    :style="{ padding: hideBtns ? '0' : '' }"
  >
    <label
      v-if="showLabel && !hideBtns"
      class="ds-form-label"
      :style="getLabelWidthStyle()"
    >
      {{ label }}
      <i v-if="dataCapion" class="iconfont icon-bangzhuzhongxin ds-control-helper" @click="showHelper"></i>
    </label>
    <div
      class="ds-form-block"
      :class="{ 'no-ds-form-label': hideBtns ? true : false }"
      :style="getFormItemBlockMarginLeft()"
    >
      <el-upload
        ref="upload"
        class="upload-demo"
        :disabled="readOnly"
        :class="{ 'upload-one-file-button': uploadOneFile }"
        :accept="uploadFileExts"
        :multiple="multiple"
        :headers="headersParam"
        :drag="isDragable"
        :limit="limitNumber"
        :action="setRequestUrl"
        :show-file-list="false"
        :before-upload="beforeUpload"
        :on-success="handleSuccess"
        :on-error="handleError"
        :on-exceed="handleExceed"
        :on-progress="handleProgress"
        :auto-upload="autoUpload"
        :style="{ display: hideBtns ? 'none' : 'block' }"
      >
        <template v-if="isDragable">
          <i class="el-icon-upload"></i>
          <div class="el-upload__text">点击上传，或将文件拖拽到此处</div>
        </template>
        <template v-else>
          <template v-if="uploadOneFile">
            <div class="one-file-button">
              {{ showBtnName ? btnName : "" }}
            </div>
            <div>
              <span class="disabled-icon">
                <i class="operation-icon el-icon-view"></i>
                <i class="operation-icon el-icon-download"></i>
                <i v-if="!readOnly" class="operation-icon el-icon-delete"></i>
              </span>
              <i v-if="!readOnly" class="operation-icon el-icon-upload2"></i>
            </div>
          </template>

          <el-button v-else type="primary" class="ds-button">
            <i class="el-icon-upload el-icon--left"></i>
            {{ showBtnName ? btnName : "" }}
          </el-button>
        </template>
      </el-upload>
      <el-button
        v-if="multipleFiles && multipleFiles.length > 0 && !uploadOneFile"
        type="primary"
        class="ds-button download-button"
        :style="{ display: hideBtns ? 'none' : 'block' }"
        @click="downLoadFn"
      >
        <i class="el-icon-download el-icon--left"></i>全部下载
      </el-button>
      <div
        v-if="uploadOneFile && multipleFiles && multipleFiles.length > 0"
        class="one-file-style"
      >
        <template v-for="item in multipleFiles">
          <div :key="item.id" class="file-item upload-table">
            <div>
              <i class="icon" :class="item.suffix"></i>{{ item.name }}.{{
                item.suffix
              }}
            </div>
            <div>
              {{ dsf.date.format(item.uploadDate, "yyyy-mm-dd hh:ii") }}
            </div>
            <div>
              {{ computeFileSize(item.size) }}
            </div>
            <div>
              <i
                class="operation-icon el-icon-view"
                @click="handleColumnClick('view', item, 0)"
              ></i>
              <i
                class="operation-icon el-icon-download"
                @click="handleColumnClick('download', item, 0)"
              ></i>
              <i
                v-if="!readOnly"
                class="operation-icon el-icon-delete"
                @click="handleColumnClick('delete', item, 0)"
              ></i>
              <el-upload
                v-if="!readOnly"
                class="list-upload"
                :accept="uploadFileExts"
                :headers="headersParam"
                :limit="limitNumber"
                :action="setRequestUrl"
                :show-file-list="false"
                :before-upload="beforeUpload"
                :on-success="handleSuccess"
                :on-error="handleError"
                :on-exceed="handleExceed"
                :auto-upload="autoUpload"
              >
                <i
                  :title="item"
                  class="operation-icon el-icon-upload2"
                  @click="handleColumnClick('upload2', item, 0)"
                ></i>
              </el-upload>
            </div>
          </div>
        </template>
      </div>
      <el-table
        v-if="multipleFiles && multipleFiles.length > 0 && !uploadOneFile"
        class="upload-table"
        :data="multipleFiles"
        :show-header="false"
        :empty-text="'暂无选择'"
      >
        <el-table-column type="index" width="30"></el-table-column>
        <el-table-column>
          <template slot-scope="scope">
            <i class="icon" :class="scope.row.suffix"></i>
            {{ scope.row.name }}.{{ scope.row.suffix }}
          </template>
        </el-table-column>
        <el-table-column width="160">
          <template slot-scope="scope">
            {{ dsf.date.format(scope.row.uploadDate, "yyyy-mm-dd hh:ii") }}
          </template>
        </el-table-column>
        <el-table-column width="100">
          <template slot-scope="scope">
            {{ computeFileSize(scope.row.size) }}
          </template>
        </el-table-column>
        <el-table-column v-if="!readOnly" label="操作" width="260" align="left">
          <template slot-scope="scope">
            <span v-for="(item, key) of handleList" :key="key">
              <i
                v-if="key !== 'upload2' && key !== 'video'"
                :title="item"
                :class="['operation-icon', `el-icon-${key}`]"
                @click="handleColumnClick(key, scope.row, scope.$index)"
              ></i>
              <el-upload
                v-if="key === 'upload2'"
                class="list-upload"
                :accept="uploadFileExts"
                :headers="headersParam"
                :limit="limitNumber"
                :action="setRequestUrl"
                :show-file-list="false"
                :before-upload="beforeUpload"
                :on-success="handleSuccess"
                :on-error="handleError"
                :on-exceed="handleExceed"
                :auto-upload="autoUpload"
              >
                <i
                  :title="item"
                  class="operation-icon el-icon-upload2"
                  @click="handleColumnClick(key, scope.row, scope.$index)"
                ></i>
              </el-upload>
              <i
                v-if="
                  key === 'video' && scope.row.suffix.toLowerCase() === 'mp4'
                "
                :data-success="isCutSuccess"
                class="operation-icon el-icon-video-camera-solid"
                :title="item"
                @click="cutFile(scope.row.id)"
              ></i>
            </span>
          </template>
        </el-table-column>
      </el-table>
    </div>

    <el-dialog
      title="修改文件名称"
      :visible.sync="editFileName"
      :close-on-click-modal="false"
    >
      <el-form :model="form">
        <el-form-item label="请输入新的名称" label-width="120px">
          <el-input
            v-model="form.name"
            autocomplete="off"
            maxlength="30"
          ></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="editFileName = false">取 消</el-button>
        <el-button type="primary" @click="editFile">
          确 定
        </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import formControl from "../../../mixins/formControl";
export default dsf.component({
  name: "DsfUploadFile",
  mixins: [formControl],
  ctrlCaption: "文件上传",
  props: {
    label: {
      type: String,
      default: "文件上传",
    },
    btnName: {
      type: String,
      default: "文件上传",
    },
    showBtnName: {
      type: Boolean,
      default: true,
    },
    multiple: {
      type: Boolean,
      default: true,
    },
    uploadFileNum: {
      type: [Number, String],
      default: 5,
    },
    uploadOneFile: {
      type: Boolean,
      default: false,
    },
    maxFileSize: {
      type: String,
      default: "",
    },
    zipFileName: {
      type: String,
      default: "",
    },
    required: {
      type: Boolean,
      default: false,
    },
    isDragable: {
      type: Boolean,
      default: false,
    },
    requestUrl: {
      type: String,
      default: "",
    },
    extsSetting: {
      type: Object,
      default() {
        return {};
      },
    },
    metadata: {
      type: Object,
      default() {
        return dsf.metadata.get("file-meta-data");
      },
    },
    hideBtns: {
      type: Boolean,
      default() {
        return false;
      },
    },
  },
  data() {
    return {
      loading: false,
      autoUpload: true,
      isShowDownloadBtn: false, // 是否显示下载按钮
      // multipleFiles: [], // 上传文件数组
      uploadFileType: "file", //默认上传类型
      uploadFileExts: "", //允许上传后缀名
      acceptMime: "", // 弹窗筛选条件MiMe类型
      headersParam: {
        "X-XSRF-TOKEN": dsf.getCookie("XSRF-TOKEN") || "",
      },
      _flowUploadClass: "ds_disposeIdea_uploadFile",
      handleList: {
        top: "上移",
        bottom: "下移",
        view: "预览",
        download: "下载",
        delete: "删除",
        upload2: "重新上传",
        // edit: "修改名称",
        video: "",
      },
      isCutSuccess: false, // 视频切片
      editFileName: false,
      form: {
        name: "",
        index: 0,
      },
      uploadFile: {
        status: false, // 当前为重新上传
        index: 0, // 当前索引
      },
    };
  },
  computed: {
    setRequestUrl() {
      return dsf.url.getWebPath(this.requestUrl || "file/upload/");
    },
    multipleFiles: {
      get() {
        let files = [];
        if (
          this.value !== "" &&
          dsf.isDef(this.value) &&
          typeof this.value === "string"
        ) {
          files = JSON.parse(this.value);
        }
        return files;
      },
      set(v) {
        this.emitValueChange(JSON.stringify(v));
      },
    },
    limitNumber() {
      if (this.uploadOneFile) {
        this.multiple = false;
        this.uploadFileNum = 100;
        this.btnName = "请点击上传文件";
        return 100;
      } else {
        this.multiple = true;
        return this.multiple ? this.uploadFileNum : 1;
      }
    },
  },
  created() {
    this.rebuildDialogData();
  },
  methods: {
    beforeUpload(file) {
      // 判断文件格式
      if (this.uploadFileExts) {
        let testmsg = file.name.split(".")[1].toLowerCase();
        if (!this.uploadFileExts.includes(testmsg)) {
          dsf.layer.message(
            `选择文件中包含不支持的文件格式,请选择包含这些格式的文件：${this.uploadFileExts}`,
            false
          );
          return false;
        }
      }
      // 判断文件大小
      let maxSize = this.maxFileSize ? this.getFileSize(this.maxFileSize) : "";
      let isMaxSize = maxSize ? file.size / 1024 < maxSize : true;
      if (!isMaxSize) {
        dsf.layer.message(`文件大小不能超过${this.maxFileSize}`, false);
        return false;
      }
      // 是否有重名
      let hasSameName = false,
        repeateName = "";
      // 上传的文件与已上传的文件名对比
      _.forEach(this.multipleFiles, (item) => {
        if (item.originalFileName == file.name) {
          hasSameName = true;
          repeateName += item.name + " ";
        }
      });
      if (hasSameName) {
        dsf.layer.message(
          `已存在文件【${repeateName}】，请修改后重新上传`,
          false
        );
        return false;
      }
    },
    handleProgress(response, file) {
      this.loading = true;
      // console.log("进度条", file.percentage.toFixed(0));
    },
    //上传成功
    handleSuccess(response, file, fileList) {
      this.loading = false;
      if (response.success && response.state == 20000) {
        let files = response.data[0];
        // 添加切片按钮
        if (files.suffix.toLowerCase() == "mp4") {
          this.isCutSuccess = this.videoIsCut(files.id); //后台是否切片成功
          this.handleList.video = this.isCutSuccess ? "切片成功" : "切片";
        }
        // 如果是单文件上传 也是替换当前文件
        // if(this.uploadOneFile){
        //   this.multipleFiles.splice(0, 1, files);
        //   this.emitValueChange(JSON.stringify(this.multipleFiles));
        // }else {
        // 重新上传 替换当前文件
        if (this.uploadFile.status) {
          this.multipleFiles.splice(this.uploadFile.index, 1, files);
          this.uploadFile.status = false;
          this.emitValueChange(JSON.stringify(this.multipleFiles));
        } else {
          this.multipleFiles.push(...response.data);
          this.emitValueChange(JSON.stringify(this.multipleFiles));
          if (this.uploadFileNum == this.multipleFiles.length) {
            dsf.layer.message(
              `当前限制选择最多 ${this.uploadFileNum} 个文件???`,
              false
            );
            this.multipleFiles.slice(0, this.uploadFileNum);
            this.emitValueChange(JSON.stringify(this.multipleFiles));
          }
        }

        // }
      } else {
        dsf.layer.message(response.message, false);
      }
    },
    //上传失败
    handleError(err, file, fileList) {
      this.loading = false;
      let errData = JSON.parse(JSON.stringify(err));
      if (errData.status == "404") {
        dsf.layer.message("服务器异常", false);
      } else {
        dsf.layer.message("上传异常", false);
      }
    },
    handleExceed(files, fileList) {
      // 同时最多只能上传数量只能为
      dsf.layer.message(
        `当前限制选择最多 ${this.uploadFileNum} 个文件---`,
        false
      );
    },
    //获取文件大小
    getFileSize(data) {
      let fileSize = parseInt(data);
      if (data.indexOf("M") > -1 || data.indexOf("m") > -1) {
        fileSize = parseInt(data) * 1024;
      }
      return fileSize;
    },
    handleColumnClick(type, row, index) {
      // console.log("row", row, index);
      const handler = {
        top: () => {
          if (index !== 0) {
            let upDate = this.multipleFiles[index - 1];
            this.multipleFiles.splice(index - 1, 1);
            this.multipleFiles.splice(index, 0, upDate);
            this.emitValueChange(JSON.stringify(this.multipleFiles));
          }
        },
        bottom: () => {
          if (index + 1 !== this.multipleFiles.length) {
            let downDate = this.multipleFiles[index + 1];
            this.multipleFiles.splice(index + 1, 1);
            this.multipleFiles.splice(index, 0, downDate);
            this.emitValueChange(JSON.stringify(this.multipleFiles));
          }
        },
        view: () => {
          let previewUrl = dsf.url.getWebPath(
            "preview/file?fileId=" + row.id + ""
          );
          window.open(previewUrl);
        },
        delete: () => {
          dsf.layer
            .confirm(`确定要删除【${row.originalFileName}】文件吗?`)
            .then(() => {
              // 删除行
              this.multipleFiles.splice(index, 1);
              this.emitValueChange(JSON.stringify(this.multipleFiles));
            })
            .catch((e) => e);
        },
        download: () => {
          var url = dsf.url.getWebPath("file/download");
          $(
            '<form action="' +
            url +
            '" method="get">' + // action请求路径及推送方法
            '<input type="text" name="files" value="' +
            row.id +
            '"/>' + // id^fileName
            '<input type="text" name="zipName" value="' +
            (row.fileName || "") +
            '"/>' + // 压缩包名称
              "</form>"
          )
            .appendTo("body")
            .submit()
            .remove();
        },
        upload2: () => {
          this.uploadFile = {
            status: true, // 当前为重新上传
            index: index, // 当前索引
          };
        },
        edit: () => {
          // 编辑名称方法-现在只对class为._flowUploadClass的上传文件开放
          this.editFileName = true;
          this.form = {
            name: row.name,
            index: index,
          };
        },
      };
      handler[type] && handler[type]();
    },
    editFile() {
      if (this.form.name) {
        let name = _.trim(this.form.name),
          files = this.multipleFiles[this.form.index];
        files.name = name;
        files.originalFileName = name + "." + files.suffix;
        this.emitValueChange(JSON.stringify(this.multipleFiles));
        this.editFileName = false;
      } else {
        dsf.layer.message("名称不能为空", false);
      }
    },
    computeFileSize(fileSize) {
      let filesizeUnit = "";
      //1048576
      if (parseInt(fileSize) > 1050000) {
        filesizeUnit = (fileSize / Math.pow(1024, 2)).toFixed(2) + "M";
      } else {
        filesizeUnit = (fileSize / Math.pow(1024, 1)).toFixed(2) + "K";
      }
      return filesizeUnit;
    },
    // 判断上传的视频是否切片
    videoIsCut(fileId) {
      let url = dsf.url.getWebPath("slice/video");
      let isCut = null;
      this.$http
        .get(url + "/" + fileId)
        .done((result) => {
          if (result.success) {
            // 获取标清切片文件
            let sdName = result.data.sd.name;
            let suffixIdx = sdName.lastIndexOf(".");
            let suffix = sdName.substring(suffixIdx + 1); //后缀
            if (suffix.toLowerCase() != "m3u8") {
              //不等于m3u8,则表示后台自动切片失败
              isCut = false;
            } else {
              isCut = true;
            }
          } else {
            dsf.layer.message("处理mp4文件" + result.message, false);
          }
          return isCut;
        })
        .error((res) => {
          dsf.layer.message(res.message, false);
        })
        .always(() => {
          return isCut;
        });
    },
    // 视频切片功能
    cutFile(fileId) {
      let url = dsf.url.getWebPath("slice/manual/video");
      this.$http
        .get(url + "/" + fileId)
        .done((res) => {
          if (res.success) {
            this.isCutSuccess = true;
            // el-icon-video-camera
          } else {
            dsf.layer.message("视频切片失败，请稍后再试", false);
          }
        })
        .error(() => {})
        .always(() => {});
    },
    downLoadFn() {
      let fileIds = [];
      let paramID = "";
      for (let i = 0; i < this.multipleFiles.length; i++) {
        let id = this.multipleFiles[i].id;
        let name = this.multipleFiles[i].name;
        let file = id + "^" + name;
        fileIds.push(file);
      }
      paramID = fileIds.join(",");
      let url = dsf.url.getWebPath("file/download");
      $(
        "<form action=" +
        url +
        ' method="get">' + // action请求路径及推送方法
        '<input type="text" name="files" value="' +
        paramID +
        '"/>' + // 文件id
        '<input type="text" name="zipName" value="' +
        this.zipFileName +
        '"/>' + // 文件名称
          "</form>"
      )
        .appendTo("body")
        .submit()
        .remove();
    },
    rebuildDialogData() {
      if (this.extsSetting.extStr) {
        let obj = this.extsSetting;
        // this.uploadFileType = 'file';//默认上传类型
        // this.uploadFileExts=""//允许上传后缀名
        // this.acceptMime =""//弹窗筛选条件MiMe类型
        this.uploadFileType = obj.fileType;
        this.isLinkeMine = obj.isLinkeMine;
        this.uploadFileExts = obj.extStr;
        this.acceptMime = obj.mineStr;
        // console.log("this.uploadFileExts", this.uploadFileExts);
        // console.log("this.acceptMime", this.acceptMime);
      }
    },
    openSelectFile() {
      this.$refs.upload.$refs["upload-inner"].handleClick();
    },
  },
});
</script>
