<template>
  <div class="ds-control ds-form-item ds-area-select" :class="getCss">
    <template v-if="readOnly&&!isDesign">{{ getShowContent() }}</template>
    <template v-else>
      <label v-if="showLabel" class="ds-form-label" :style="getLabelWidthStyle()">
        {{ label }}
        <i v-if="dataCapion" class="iconfont icon-bangzhuzhongxin ds-control-helper" @click="showHelper"></i>
      </label>
      <div class="ds-form-block" :style="getFormItemBlockMarginLeft()">
        <div v-if="!readOnly" class="ds-area-select-wrap">
          <el-select v-if="showProvince" v-model="province" clearable value-key="treeinfo_globalid" :disabled="isDisabled(provinceList)" placeholder="请选择省" @change="(v) => onChange(v, 1)">
            <el-option
              v-for="el in provinceList"
              :key="el.treeinfo_globalid"
              :label="el.name" 
              :value="el"
            ></el-option>
          </el-select>
          <el-select v-if="showCity" v-model="city" clearable value-key="treeinfo_globalid" :disabled="isDisabled(cityList)" placeholder="请选择市" @change="(v) => onChange(v, 2)">
            <el-option
              v-for="el in cityList" 
              :key="el.treeinfo_globalid" 
              :label="el.name" 
              :value="el"
            ></el-option>
          </el-select>
          <el-select v-if="showDist" v-model="dist" clearable value-key="treeinfo_globalid" :disabled="isDisabled(distList)" placeholder="请选择县/区" @change="(v) => onChange(v, 3)">
            <el-option
              v-for="el in distList" 
              :key="el.treeinfo_globalid"
              :label="el.name" 
              :value="el"
            ></el-option>
          </el-select>
          <el-select v-if="showStreet" v-model="street" clearable value-key="treeinfo_globalid" :disabled="isDisabled(streetList)" placeholder="请选择街道" @change="(v) => onChange(v, 4)">
            <el-option
              v-for="el in streetList" 
              :key="el.treeinfo_globalid"
              :label="el.name" 
              :value="el"
            ></el-option>
          </el-select>
          <el-select v-if="showTown" v-model="town" clearable value-key="treeinfo_globalid" :disabled="isDisabled(townList)" placeholder="请选择村居" @change="(v) => onChange(v, 5)">
            <el-option
              v-for="el in townList" 
              :key="el.treeinfo_globalid"
              :label="el.name" 
              :value="el"
            ></el-option>
          </el-select>                                                
        </div>        
        <!--只读状态只显示div-->
        <div v-else-if="readOnly" class="ds-form-readonly">
          <span>{{ getShowContent() }}</span>
        </div>
      </div>
    </template>
  </div>
</template>
<script>
import formControl from "../../../mixins/formControl";
export default dsf.component({
  name: "DsfAreaSelect",
  mixins: [formControl],
  ctrlCaption: "行政区划",
  props: {
    placeholder: {
      type: String,
      default: "请选择"
    },
    value: {
      type: Object,
      default() {
        return {};
      }
    },
    superCode: {
      type: [String, Number],
      default: '000000000000',
    },
    fixedCode: {
      type: String,
      default: '',
    },
    range: {
        type: Array,
        default() {
            return ['0', '4'];
        }
    },
    metadata: {
      type: Object,
      default() {
        return dsf.metadata.get("items-meta-data");
      }
    }
  },
  data() {
      return {
        province: {},
        city: {},
        dist: {},
        street: {},
        town: {},
        data: [],
      }
  },
  watch: {
    superCode(v) {
      if (v && v.length === 12) {
        const [ level, gid ] = this.getLevelAndGlobalid(v);
        if (level) {
          this.$emit('update:range', [String(level - 2), String(this.endRange)]);
        }
      }
    }
  },
  computed: {
    startRange() {
      return parseInt(this.range[0], 10);
    },
    endRange() {
      return parseInt(this.range[1], 10);
    },
    showProvince() {
      return this.startRange === 0;
    },
    showCity() {
      return this.startRange <= 1 && this.endRange >= 1;
    },
    showDist() {
      return this.startRange <= 2 && this.endRange >= 2;
    },
    showStreet() {
      return this.startRange <= 3 && this.endRange >= 3;
    },
    showTown() {
      return this.startRange <= 4 && this.endRange >= 4;
    },
    provinceList() {
      return this.data;
    },
    cityList() {
      let d = [];
      if(this.startRange === 1) {
        d = this.data;
      } else if (this.province && this.province.treeinfo_globalid) {
        const obj = this.provinceList.find(it => it.treeinfo_globalid === this.province.treeinfo_globalid);
        d = obj && obj.children ? obj.children : [];
      }
      return d;
    },
    distList() {
      let d = [];
      if(this.startRange === 2) {
        d = this.data;
      } else if (this.city && this.city.treeinfo_globalid) {
        const obj = this.cityList.find(it => it.treeinfo_globalid === this.city.treeinfo_globalid);
        d = obj && obj.children ? obj.children : [];
      }
      return d;
    },
    streetList() {
      let d = [];
      if(this.startRange === 3) {
        d = this.data;
      } else if (this.dist && this.dist.treeinfo_globalid) {
        const obj = this.distList.find(it => it.treeinfo_globalid === this.dist.treeinfo_globalid);
        d = obj && obj.children ? obj.children : [];
      }
      return d;
    },
    townList() {
      let d = [];
      if(this.startRange === 4) {
        d = this.data;
      } else if (this.street && this.street.treeinfo_globalid) {
        const obj = this.streetList.find(it => it.treeinfo_globalid === this.street.treeinfo_globalid);
        d = obj && obj.children ? obj.children : [];
      }
      return d;
    }
  },
  methods: {
      isFixed(it) {
        const [, globalid ] = this.getLevelAndGlobalid(this.fixedCode);
        return globalid.includes(it.treeinfo_globalid);
      },
      isDisabled(target) {
        let flag = false;
        if (this.fixedCode) {
          flag = target.length === 1 && target.every(it => {
            return this.isFixed(it);
          });
        }
        return this.disabled ? true : flag;
      },
      getCodeArr(code) {
        const str = String(code);
        const arr = [];
        let reduceNum = 2;
        for(let i = 0; i < str.length; i += reduceNum) {
          if (i === 6) {
            reduceNum = 3;
          }          
          arr.push(str.slice(i, i + reduceNum));
        }
        return arr;
      },
      getLevelAndGlobalid(id) {
        const arr = this.getCodeArr(id);
        const [ level, globalid ] = arr.reduce((a, b) => {
          let [ level, globalid ] = a;
          if (parseInt(b, 10) > 0) {
            level += 1;
            const g = Array.from(globalid[globalid.length - 1]);
            const index = id.indexOf(b);
            g[index] = b.slice(0,1);
            g[index + 1] = b.slice(1,2);
            if (b.length === 3) {
              g[index + 2] = b.slice(2,3);
            }
            globalid.push(g);
          }
          return [level, globalid];
        }, [2, [new Array(12).fill(0)]]);
        return [level, globalid.map(it => it.join('')).join('.'), arr]
      },
      getNodeById(arr, id) {
        let target = null;
        for(let item of arr) {
          if (item.treeinfo_globalid === id) {
            target = item;
          } else if(item.children && item.children.length) {
            target = this.getNodeById(item.children, id);
          }
          if (target) {
            break;
          }
        }
        return target;
      },
      componentValueChange() {
        let target, text;
        switch(this.endRange) {
          case 4:
            target = this.town;
            text = `${this.province.name}-${this.city.name}-${this.dist.name}-${this.street.name}-${this.town.name}`;
            break;
          case 3:
            target = this.street;
            text = `${this.province.name}-${this.city.name}-${this.dist.name}-${this.street.name}`;
            break;
          case 2:
            target = this.dist;
            text = `${this.province.name}-${this.city.name}-${this.dist.name}`;
            break;
          case 1:
            target = this.city;
            text = `${this.province.name}-${this.city.name}`;
            break;
          case 0:
            target = this.province;
            text = `${this.province.name}`;
            break;
        }
        if (target && target.areacode) {
          const data = {
            value: target.areacode,
            text,
          };
          this.emitValueChange(data);
          this.emitFormValidate(data);           
        }        
      },
      setValueData(it, isUpdate = false) {
        switch(it.treeinfo_level - 1) {
          case 1:
            this.province = Object.assign({}, isUpdate ? this.province : it);
            break;
          case 2:
            this.city = Object.assign({}, isUpdate ? this.city : it);
            break;
          case 3:
            this.dist = Object.assign({}, isUpdate ? this.dist : it);
            break;
          case 4:
            this.street = Object.assign({}, isUpdate ? this.street : it);
            break;
          case 5:
            this.town = Object.assign({}, isUpdate ? this.town : it);
            break;
        }        
      },
      loadData(node = null) {
        const range = this.range;
        const startLevel = this.startRange + 2;
        const endLevel = this.endRange + 2;
        const level = node && node.treeinfo_level ? parseInt(node.treeinfo_level, 10) + 1 : startLevel;
        const [, gid] = this.getLevelAndGlobalid(this.superCode);
        const globalid = node && node.treeinfo_globalid ? node.treeinfo_globalid : gid;
        let params = {
            TREEINFO_GLOBALID: globalid,
            END_LEVEL: level,
            START_LEVEL: level,
        };
        if (!node && !this.data.length && this.fixedCode) {
          const [ level, gid ] = this.getLevelAndGlobalid(this.fixedCode);
          params.TREEINFO_GLOBALID = gid.split('.').slice(0,2).join('.');
          params.END_LEVEL = level;
        }        
        this.$http.get('area/getArea', params).then(({ data }) => {
            if (data.success) {
              const fn = arr => {
                let temp = [...arr];
                temp = temp.map(it => {
                  if (this.fixedCode && this.isFixed(it)) {
                    this.setValueData(it);
                  }
                  if (this.value && this.value.value) {
                    if (it.areacode === this.value.value) {
                      this.setValueData(it);
                    }
                  }
                  return {
                    ...it,
                    loaded: it.children && it.children.length ? true : false,
                    children: it.children && it.children.length ? fn(it.children) : [],
                  }
                });
                return temp;
              }
              const d = fn(data.data);
              if (node) {
                const target = this.getNodeById(this.data, node.treeinfo_globalid);
                if (target) {
                  target.loaded = true;
                  target.children = d;
                  this.setValueData(target, true);
                }
              } else {
                this.data = d;
              }
            }
        }); 
      },
      onChange(v, type) {
          if (!v.loaded) {
            switch(type){
              case 1:
                this.city = {};
                this.dist = {};
                this.street = {};
                this.town = {};
                break;
              case 2:
                this.dist = {};
                this.street = {};
                this.town = {};
                break;
              case 3:
                this.street = {};
                this.town = {};
                break;
              case 4:
                this.town = {};
                break;
            }
            this.loadData(v);
          }
          this.componentValueChange();
      }
  },
  created() {
    if (!this.isDesign) {
      this.loadData();
    }
  },
  design: {
    layout: {
      exclude: ["value", "items"]
    }
  }
});
</script>
