<!--template>
  <a :class="getCss" :title="title" v-on="$listeners">
    <DsfIcon v-if="icon" :name="icon ? icon : ''" />
    <span v-if="text">{{ text }}</span>
    <template v-else>
      <span>
        <slot></slot>
      </span>
    </template>
  </a>
</template-->
<script>
export default {
  name: "DsfButton",
  props: {
    tagName: {
      type: String,
      default: "a"
    },
    type: {
      //auxiliary为次要按钮
      type: String,
      default() {
        return "";
      }
    },
    icon: {
      type: String,
      default() {
        return "";
      }
    },
    text: {
      type: String,
      default() {
        return "";
      }
    },
    title: {
      type: String,
      default() {
        return "";
      }
    },
    btnStyle: {
      type: String,
      default() {
        // 0:普通按钮 2：图标按钮 4:文字加图标 5：小图标
        return "";
      }
    },
    size: {
      type: String,
      default: ""
    }
  },
  computed: {
    buttonStyle() {
      let s = this.btnStyle;
      // switch (this.btnStyle) {
      //   case "0":
      //     s = "";
      //     break;
      //   case "2":
      //     s = "icon-button";
      //     break;
      //   case "4":
      //     s = "icon-text-button";
      //     break;
      //   case "5":
      //     s = "icon-text-button";
      //     break;
      // }
      return s;
    },
    getCss() {
      return [
        'ds-button' + (this.btnStyle ? "__" + this.btnStyle : ''),
        this.type ? this.type : '',
        this.size
      ]
    }
  },
  created() {

  },
  methods: {

  },
  render(h) {
    let attrs = {
      title: this.title || ""
    };
    let notText = false;
    let span = null;
    if (this.$slots.default&&this.$slots.default[0]&&this.$slots.default[0].text) {
      span = h('span', this.$slots.default);
    }
    else if (this.text) {
      span = h('span', this.text);
    }
    else {
      notText = true;
    }
    let child = [];
    if (this.icon) {
      let icon = h("i", {
        staticClass: "iconfont",
        class: {
          ["icon-" + this.icon]: true,
        }
      });
      child.push(icon);
    }
    child.push(span);
    return h(this.tagName, {
      attrs: attrs,
      class: [...this.getCss,(notText?'no-text':'')],
      on: this.$listeners,
    }, child)
  }
};
</script>
