<template>
  <div v-loading.fullscreen="loading" class="ds-control ds-no-padding">
    <template v-if="isDesign">
      <div v-if="isDesign" style="text-align:center;background-color:#fff;padding:3px 0px;font-style:italic;color:#c0c0c0">
        动态表单
      </div>
    </template>
    <template v-else>
      <currentForm v-if="isLoad"></currentForm>
    </template>
  </div>
</template>

<script>
import control from "_platform/dsf/mixins/control";
import dynamicFormTemplate from "./js/dynamicFormTemplate";

export default dsf.component({
  name: "DsfDynamicForm",
  mixins: [control],
  ctrlCaption: "动态表单",
  provide() {
    return {
      $dynamicForm: this
    }
  },
  props: {
    isDynamicForm: {
      type: Boolean,
      default: true
    },
    url: {
      type: String,
      default: "/dynamic/meta"
    },
    prefix: {
      type: String,
      default: "dynamicForm"
    },
    ids: {
      type: Array,
      default() {
        return [];
      }
    },
    cmIds: {
      type: Array,
      default() {
        return [];
      }
    },
    idcard: {
      type: String,
      default: ""
    },
    baseinfoKey: {
      type: [Array, String],
      default() {
        return []
      }
    },
    // 子组件二次开发
    childrenDev: {
      type: Object,
      default() {
        return {}
      }
    }
  },
  data() {
    return {
      isLoad: false,
      loading: false,
      $components: [],
      components: [],
      lastData: {},
      changing: false
    };
  },
  computed: {
    $prefix() {
      return "~" + this.prefix;
    }
  },
  created() {
    if (!this.isDesign) {
      document.body.classList.add("__dynamicForm");
      if (this.$vm && this.$form) {
        this.$form.addFiled(this);
      }
    }
    //监听身份证号码和事项是否变化
    this.$watch(function () {
      return this.ids.join(",") + "|" + this.idcard
    }, function (v, o) {
      if (o && v) {
        let newArr = v.split("|");
        let oldArr = o.split("|");
        if (oldArr[1] && newArr[1] !== oldArr[1]) {
          this.resetForm();
        }
      }
      this.changeForm()
    }, {
      immediate: true
    })
  },
  destroyed() {
    if (this.$options.components.currentForm) {
      this.$options.components.currentForm = null;
      delete this.$options.components.currentForm;
    }
    document.body.classList.remove("__dynamicForm");
  },
  methods: {
    changeForm() {
      // this.bakOldDataKeys();
      if (this.ids.length > 0 && !this.changing) {
        this.changing = true;
        this.loading = true;
        let params = { 'rmIds': this.ids.join(","), cmIds: this.cmIds.join(",") };
        let params2 = { 'rmIds': this.ids.join(","), idCard: this.idcard };
        dsf.mix(params, this.$vm.queryString);
        // console.log(params2)
        let getLayout = this.$http.get(this.url, params);
        let getLastData = this.$http.get("/dynamic/history", params2);
        getLayout.
          then(({ data }) => {
            this.isLoad = false;
            this.$data.$components = _.map(data.data, (el) => {
              el.controls = _.map(el.controls, (control) => {
                return {
                  dict: control.dict || null,
                  props: JSON.parse(control.content)
                }
              });
              return el;
            })
            return getLastData
          })
          .then(({data}) => {
            this.lastData = data.data;
          })
          .catch((ex) => {
            dsf.error(ex);
            dsf.layer.message("表单加载出现错误", false);
          })
          .finally(() => {
            this.loading = false;
            this.changing = false;
            this.$nextTick(() => {
              this.loadForm();
            });
          })

        this.$http.all([getLayout, getLastData])
          .then((resultList) => {
            this.isLoad = false;
            _.each(resultList, (result, i) => {

            });
          })
          .catch(() => {
            dsf.layer.message("动态表单加载出错", false);
          })
          .finally(() => {
            this.loading = false;
          })
        // this.$http.get(this.url, params)
        //   .then(({ data }) => {
        //     this.isLoad = false;
        //     this.$data.$components = _.map(data.data, (el) => {
        //       return {
        //         dict: el.dict || null,
        //         props: JSON.parse(el.content)
        //       }
        //     })
        //     this.$nextTick(() => {
        //       this.loadForm();
        //     });
        //   })
        //   .catch((ex) => {
        //     dsf.layer.message("动态表单加载出错", false);
        //   })
        //   .finally(() => {
        //     this.loading = false;
        //   })
      }
      else {
        this.isLoad = false;
        this.$data.$components = [];
      }
    },
    loadForm() {
      this.$options.components.currentForm = dynamicFormTemplate(this);
      this.isLoad = true;
    },
    addFiled(ctrl) {
      //将不在子表内的表单组建添加到components
      if (ctrl.isFormControl && !ctrl.$list) {
        this.components.push(ctrl);
      }
    },
    removeFiled(ctrl) {
      dsf.array.remove(this.components, ctrl);
    },
    resetForm() {
      _.each(this.$vm.$viewData, (v, i) => {
        if (i.startsWith(this.$prefix)) {
          this.$delete(this.$vm.$viewData, i);
        }
      });
    }
  },
  watch: {
    // 'ids': {
    //   handler(v) {
    //     if (!this.isDesign) {
    //       this.changeForm()
    //     }
    //   },
    //   immediate: true
    // },
    // 'idcard': {
    //   handler(v, o) {
    //     if (!this.isDesign) {
    //       if (o && v != o) {
    //         this.resetForm();
    //       }
    //       this.changeForm()
    //     }
    //   }
    // }
  },
});
</script>
