import DsfOneThingBanner from "./DsfOneThingBanner";
import DsfOneThingMessage from "./DsfOneThingMessage";
import DsfOneThingService from "./DsfOneThingService";
import DsfOneThingApplication from "./DsfOneThingApplication";
import DsfOneThingFormSelect from "./DsfOneThingFormSelect";
import DsfOneThingSceneSelect from "./DsfOneThingSceneSelect";
import DsfDynamicForm from './DynamicForm';
import DsfOneThingSection from './DsfOneThingSection';

function install(Vue) {
  Vue.component(DsfOneThingMessage.name, DsfOneThingMessage);
  Vue.component(DsfOneThingBanner.name, DsfOneThingBanner);
  Vue.component(DsfOneThingService.name, DsfOneThingService);
  Vue.component(DsfOneThingApplication.name, DsfOneThingApplication);
  Vue.component(DsfOneThingFormSelect.name, DsfOneThingFormSelect);
  Vue.component(DsfOneThingSceneSelect.name, DsfOneThingSceneSelect);
  Vue.component(DsfDynamicForm.name, DsfDynamicForm);
  Vue.component(DsfOneThingSection.name, DsfOneThingSection);
}

if (typeof window !== "undefined" && window.Vue) {
  install(window.Vue);
}

export {
  install,
  DsfOneThingBanner,
  DsfOneThingMessage,
  DsfOneThingService,
  DsfOneThingApplication,
  DsfOneThingFormSelect,
  DsfOneThingSceneSelect,
  DsfDynamicForm,
  DsfOneThingSection
};

export default {
  install,
};