<template>
  <DsfSection
    :is-design="isDesign"
    :margin-top="marginTop"
    :margin-bottom="marginBottom"
    :margin-left="marginLeft"
    :margin-right="marginRight"
    :padding-top="paddingTop"
    :padding-bottom="paddingBottom"
    :padding-left="paddingLeft"
    :padding-right="paddingRight"
    :margin-size="marginSize"
  >
    <DsfTitle3 v-if="isDesign || hiddenControls.length" :value="label" :padding-left="labelWidth" show-weight>
      <span v-show="!hiddenType" class="__dynamicForm-section-more" @click="toggel">
        <span>查看更多</span>
        <i class="iconfont icon-xia1"></i>
      </span>
      <span v-show="hiddenType" class="__dynamicForm-section-more" @click="toggel">
        <span>收起更多</span>
        <i class="iconfont icon-shang1"></i>
      </span>
    </DsfTitle3>
    <DsfTitle3 v-else :value="label" show-weight></DsfTitle3>
    <dsf-row ref="row" :is-design="isDesign" slot-name="default" :style="[isDesign ? {'min-height': '60px'} : {}]">
      <slot></slot>
    </dsf-row>
  </DsfSection>
</template>

<script>
import layout from "_platform/dsf/mixins/layout";

export default dsf.component({
  name: 'DsfOneThingSection',
  ctrlCaption: "段落",
  mixins: [layout],
  props: {
    label: {
      type: String,
      default: '段落标题'
    },
    slots: {
      type: Array,
      default() {
        return [
          { name: 'default', controls: [] }
        ];
      }
    },
    marginTop: {
      type: Boolean,
      default: false
    },
    marginBottom: {
      type: Boolean,
      default: false
    },
    marginLeft: {
      type: Boolean,
      default: false
    },
    marginRight: {
      type: Boolean,
      default: false
    },
    paddingTop: {
      type: Boolean,
      default: true
    },
    paddingBottom: {
      type: Boolean,
      default: true
    },
    paddingLeft: {
      type: Boolean,
      default: true
    },
    paddingRight: {
      type: Boolean,
      default: true
    },
    marginSize: {
      type: String,
      default: "small"
    }
  },
  data() {
    return {
      hiddenType: false,
      hiddenControls: []
    }
  },
  computed: {
    labelWidth () {
      return this.label.length * 18 + 28 + 'px';
    }
  },
  mounted() {
    if (!this.isDesign) {
      const hiddenControls = [];
      $.each($(this.$refs.row.$el).find('.ds-form-item'), (i, el) =>  {
        const vm = el.__vue__;
        if (!vm.props.visible) {
          hiddenControls.push(vm);
        }
      });
      this.hiddenControls = hiddenControls;
    }
  },
  methods: {
    toggel() {
      if (this.isDesign) return;
      this.hiddenType = !this.hiddenType;
      this.hiddenControls.forEach(vm => {
        vm.$emit('update:visible', this.hiddenType);
      });
      this.$nextTick(() => {
        const dom = document.getElementsByClassName('ds-one-thing-form-select');
        if (dom && dom.length && dom[0].__vue__) {
          dom[0].__vue__.onScroll();
        }
      });
    }
  }
});
</script>