<!-- 问卷 -->
<template>
  <div v-show="showCtrl" class="ds-control ds-question-item ds-radio" :class="{'ds-qi-has-rela':!hasRela,'ds_hide':!visible}">
    <DsfQITitle :required="required" :question-index="questionIndex" :question-body="questionBody"></DsfQITitle>
    <el-checkbox-group v-model="checkValue" :disabled="readOnly" @change="change">
      <template v-if="layout == '0'">
        <el-checkbox v-for="item in items" :key="item.value" :label="item.value">{{ item.text }}</el-checkbox>
        <el-checkbox v-if="isOther && !hasOther" label="else">
          其他
        </el-checkbox>
      </template>
      <el-row v-else>
        <el-col v-for="item in items" :key="item.value" :span="optionCol">
          <el-checkbox :label="item.value">{{ item.text }}</el-checkbox>          
        </el-col>
        <el-col v-if="isOther && !hasOther" :span="optionCol">
          <el-checkbox label="else">
            其他
          </el-checkbox>        
        </el-col>
      </el-row>
    </el-checkbox-group>
  </div>
</template>

<script>
import questionControl from "_platform/dsf/mixins/questionControl";

export default dsf.component({
  name:'DsfQICheckBox',
  ctrlCaption: "多选题",
  mixins: [questionControl],
  data () {
    return {
      valueCopy: []
    }
  },
  props:{
    items:{
      type:Array,
      default() {
        return [{text:'选项1',value:1},{text:'选项2',value:2}]
      }
    },
    metadata: {
      type: Object,
      default() {
        return dsf.metadata.get("items-meta-data");
      }
    },
    // 是否其他
    isOther: {
      type: Boolean,
      default: false,
    },
    // 排列样式
    layout: {
      type: [String, Number],
      default: '0'
    },
    mustCheckNum: {
      type: Number,
      default: 0,
    },
    maxCheckNum: {
      type: Number,
      default: 0,
    }
  },
  components: {},

  computed: {
    checkValue: {
      get() {
        if (dsf.type(this.valueCopy) === "array") {
          return this.valueCopy.map(v => v.value);
        }
        return [];
      },
      set(v) {
        this.initValue(v);
      }
    },

     // 判断items中是否已经存在其他
    hasOther() {
      return this.items.findIndex((v) => v.value === "else") > -1;
    },
    optionCol() {
      switch(this.layout.toString()) {
        case '1':
          return 24;
        case '2':
          return 12;
        case '3':
          return 8;
        case '4':
          return 6;
        case '5':
          return 4;
        default:
          return 24;
      }
    },    
  },

  mounted() {},

  created() {
    this.initValue(this.value);
  }, 

  watch: {
    // 监听值
    value(to) {
      this.initValue(to);
    },
  },

  methods:{
    change(v) {
      console.log(this.valueCopy)
    },
    initValue(to) {
      if (!this.readOnly) {
        let nv;
        if (to === void 0 || to === null) {
          nv = [];
        } else if (dsf.type(to) === "array") {
          nv = this.filterValue(to);
        } else {
          nv = this.filterValue([to]);
        }
        if (dsf.type(nv) === "array" && !this.isSame(nv, this.valueCopy)) {
          this.valueCopy = nv;
          this.updateValue(nv);
        }
      }
    },
    filterValue(value) {
      return value.map(v => {
        if (typeof v === "object" && v.value !== void 0) {
          return this.getObjectByValue(v.value);
        } else if (typeof v === "string" ||
          typeof v === "number" ||
          typeof v === "boolean") {
          return this.getObjectByValue(v);
        }
        return void 0;
      }).filter(v => v !== void 0);
    },
    getObjectByValue (v) {
      if(v=='else') {
        return {
          text: '其他', value: v
        }
      }
      for (let i = 0; i < this.items.length; i++) {
        let item = this.items[i];
        if (v === item.value) {
          return {
            text: item.text, value: v
          };
        }
      }
      return void 0;
    },
    // 判断两个值是否相同
    isSame(p1, p2) {
      if (dsf.type(p1) === "array" && dsf.type(p2) === "array") {
        if (p1.length !== p2.length) return false;
        for (let i = 0; i < p1.length; i++) {
          let same = false;
          for (let j = 0; j < p2.length; j++) {
            if (p1[i].text === p2[j].text && p1[i].value === p2[j].value) {
              same = true;
              break;
            }
          }
          if (!same) {
            return false
          }
        }
        return true;
      }
      return p1 === p2;
    },
    updateValue(to) {
      this.emitValueChange(to);
      this.emitFormValidate(to);
    }
  },

  design: {
    layout: {
      //#开头表示该属性在预览模式下会被保留
      exclude: ["value", "#items"],
    }
  }
})

</script>