<!-- 问卷 -->
<template>
  <div v-show="showCtrl" class="ds-control ds-question-item ds-fill" :class="{'ds-qi-has-rela':!hasRela,'ds_hide':!visible}">
    <p :class="['ds-question-body',{'ds-question-require':required}]">
      <span class="ds-question-index">{{ questionIndex?(questionIndex+'：'):'' }}</span>
      <template v-for="(ele,index) in html">
        {{ ele }}
        <template v-if="index<html.length-1 && html.length>2">
          <input :key="index" v-model="answer['text_'+(index+1)]" type="text" class="ds-qi-fill" :maxlength="max" :readonly="readOnly" @input="setWidth" />
        </template>
        <template v-if="index<html.length-1 && html.length<=2">
          <input :key="index" v-model="answer" type="text" class="ds-qi-fill" :maxlength="max" :readonly="readOnly" @input="setWidth" />
        </template>
      </template>
      <span ref="font" class="ds-qi-fill font-pre"></span>
    </p>
  </div>
</template>

<script>
import questionControl from "_platform/dsf/mixins/questionControl";

export default dsf.component({
  name:'DsfQIFill',
  ctrlCaption: "填空题",
  mixins: [questionControl],
  data () {
    return {
      inputValue:'',
      answer:'',
      html:[]
    };
  },

  props:{
    placeholder:{
      type:String,
      default:'请输入'
    },
    max:{
      type:[Number,String],
      default:200
    }
  },

  watch:{
    questionBody:function(val) {
      this.renderInput(val)
    }
  },

  components: {},

  computed: {},

  mounted() {},

  created() {
    this.renderInput(this.questionBody);
  },

  methods: {
    setWidth(e) {
      this.$refs.font.innerHTML = e.target.value;
      e.target.style.width = $(this.$refs.font).width() + 'px'
    },
    /**
     * 通过（）分割判断渲染输入框数量
     */
    renderInput(val) {
      let str = val.replace(/（）/gi, '()');
      this.html = str.split("()");
      //判断长度的原因，多个输入框存在的情况，值为对象
      if(this.html.length>2){
        !window._.isArray(this.answer)&&(this.answer=[]);
        window._.forEach(this.html,(item,i)=>{
          i>0 && (this.answer[`text_${i}`] = "");
        })
      }else{
        this.answer=""
      }
    }
  }
})

</script>