<template>
  <div class="ds-control ds-datagrid" :class="getCss">
    <DsfPanel class="ds-datagrid-panel" :style="{height:panelHeight}" :has-title="showTitle" :has-header="showHead" :title-border="true">
      <template v-slot:title>{{ label }}</template>
      <template v-slot:header>
        <div class="ds-datagrid-header">
          <div class="ds-datagrid-header-right">
            <div v-if="checkboxColumn.show" class="head-right-item">
              <span class="ds-datagrid-checkednumber ds_font5">
                <i class="icon-093info iconfont layui-icon icon-color ds_icon_4" style="margin-right:5px;"></i>已选：<font handler="checkedNumber" style="margin:0px 5px;">{{ multipleSelection.length }}</font>条
              </span>
            </div>
            <div v-if="showSearch" class="head-right-item" style="margin-left:10px">
              <form @submit.stop.prevent="search">
                <!-- <DsfTextbox v-model="searchStr" :placeholder="searchPlaceholder" class="search-input"></DsfTextbox> -->
                <el-input v-model="searchStr" :placeholder="searchPlaceholder" class="search-input"></el-input>
                <button class="search-button"><i class="iconfont icon-fangdajing"></i></button>
              </form>
            </div>
            <div v-if="buttons.length" class="head-right-item" style="margin-left:10px">
              <DsfButtonBar :buttons="buttons" :owner="_self" :is-design="isDesign" :$local="$local" :class="{'ds-no-padding':true}"></DsfButtonBar>
            </div>
          </div>
        </div>
      </template>
      <template v-slot:default>
        <div class="ds-datagrid-search-wrap">
          <div v-if="superSearchInfo.length>0" :style="{height:tableMarginTop+'px'}">
            <form @submit.stop.prevent="search">
              <div style="float:left;min-height:1px;" :style="{'width':(superSearchButtonTrigger?'calc(100% - 120px)':'100%')}">
                <DsfSuperSearch :show-fitler="showSuperSearchFilter" :search-item-setting="superSearchInfo" :value.sync="superValue" :owner="_self" :search-height.sync="superSearchHeight" :default-rows-number="superSearchDefaultRowsNumber"></DsfSuperSearch>
              </div>
              <div v-if="superSearchButtonTrigger" class="ds-datagrid-search-wrap-buttons" style="float:left;width:120px;">
                <button class="ds-button" style="float:right"><i class="iconfont icon-fangdajing"></i>检索</button>
              </div>
            </form>
          </div>
        </div>
        <div class="ds-datagrid-content-wrap" :style="{'height':contentWrapHeight}">
          <el-table v-if="isDesign" ref="table" :border="true" :data="templateData" :height="tableHeight" stripe>
            <el-table-column v-if="checkboxColumn.show" type="selection" label="" :width="60" header-align="center" align="center"></el-table-column>
            <el-table-column v-if="indexColumn.show" :type="'index'" :index="indexMethod" :label="'序号'" :resizable="false" :width="indexColumn.width" :header-align="'center'" :align="'center'"></el-table-column>
            <slot></slot>
            <template v-if="!isReDrawHander">
              <DsfDataGridColumn v-for="(col, $index) in columns" ref="header" :key="$index" :column="col" :sortable="getColumnSort(col)">
                <template v-for="column in getLastColumns(col)" v-slot:[column.columnId]="scope">
                  <slot :name="column.columnId" v-bind="scope"></slot>
                </template>
              </DsfDataGridColumn>
              <el-table-column v-if="operateColumn.show" label="操作" :width="operateColumn.width" header-align="center" align="center">
                <template v-slot:default="scope">
                  <DsfButtonBar size="small" class="ds-no-padding ds-no-drop" :is-design="isDesign" :buttons="scope._self.rowButtons" align="center"></DsfButtonBar>
                </template>
              </el-table-column>
            </template>
          </el-table>
          <!--非设计状态-->
          <el-table
            v-else
            ref="table"
            v-loading="loading"
            :border="true"
            :fit="true"
            :data="data"
            :height="tableHeight"
            :row-key="'_id'"
            stripe
            :header-cell-class-name="getHeaderClass"
            @selection-change="handleSelectionChange"
            @header-dragend="headerDragend"
            @row-dblclick="rowDblClickHandler"
            @sort-change="tableSort"
          >
            <el-table-column v-if="checkboxColumn.show" type="selection" label="" :reserve-selection="true" :width="60" header-align="center" align="center" fixed></el-table-column>
            <el-table-column v-if="indexColumn.show" type="index" label="序号" :width="indexColumn.width" header-align="center" align="center" fixed></el-table-column>
            <slot></slot>
            <template v-if="!isReDrawHander">
              <DsfDataGridColumn v-for="(col, $index) in columns" ref="header" :key="$index" :column="col" :sortable="getColumnSort(col)">
                <template v-for="column in getLastColumns(col)" v-slot:[column.columnId]="scope">
                  <slot :name="column.columnId" v-bind="scope"></slot>
                </template>
              </DsfDataGridColumn>
            </template>
            <el-table-column v-if="operateColumn.show" label="操作" :width="operateColumn.width" header-align="center" align="center" fixed="right">
              <template v-slot:default="scope">
                <slot name="buttonColumns" v-bind="scope">
                  <div style="padding-top:3px;padding-bottom:3px;">
                    <DsfButtonBar size="small" :buttons="scope._self.rowButtons" :emit-prefix="'row-button'" :owner="_self" align="center" :$local="scope.row" class="ds-no-padding "></DsfButtonBar>
                  </div>
                </slot>
              </template>
            </el-table-column>
          </el-table>
        </div>
        <div v-if="showPagination" class="ds-datagrid-pager">
          <el-pagination :hide-on-single-page="false" :total="pageCount" :page-sizes="limits" :current-page.sync="currentPage" background :layout="pagerLayout" @size-change="pageSizeChange"></el-pagination>
        </div>
      </template>
    </DsfPanel>
  </div>
</template>
<script>
import control from '_platform/dsf/mixins/control';
import dataResult from '_platform/dsf/mixins/dataResult';
import DsfDataGridCellContent from './datagridCellContent';
import DsfSuperSearch from './superSearch';
// import elTableInfiniteScroll from 'el-table-infinite-scroll';
let tableColumnSlotName = "table-column";
let cardSlotName = "card"
export default dsf.component({
  name: "DsfDataGrid",
  ctrlCaption: "数据列表",
  mixins: [control, dataResult],
  components: { DsfSuperSearch },
  provide() {
    return {
      $list: this
    }
  },
  props: {
    // templateInFor: {
    //   type: Boolean,
    //   default: true
    // },
    buttons: {
      type: Array,
      default() {
        return [];
      }
    },
    rowButtons: {
      type: Array,
      default() {
        return [];
      }
    },
    //分页页码按钮最多显示个数
    pagerCount: {
      type: Number,
      default: 5
    },
    //一页显示多少条记录
    pageSize: {
      type: Number,
      default: 15
    },
    //列头
    columns: {
      type: Array,
      default() {
        return [];
      }
    },
    //高度是否自适应
    fit: {
      type: Boolean,
      default: true
    },
    //是否显示头部
    showHead: {
      type: Boolean,
      default: true
    },
    //是否显示标题
    showTitle: {
      type: Boolean,
      default: true
    },
    showSearch: {
      type: Boolean,
      default: true
    },
    //每页显示条目
    limits: {
      type: Array,
      default() {
        return [15, 20, 30, 40, 50, 100]
      }
    },
    //标题
    label: {
      type: String,
      default: "数据列表"
    },
    //设计模式下的模板
    templateData: {
      type: Array,
      default() {
        return [{}];
      }
    },
    //是否显示分页
    showPagination: {
      type: Boolean,
      default: true
    },
    //分页组建的布局
    pagerLayout: {
      type: String,
      default: "total,prev,pager,next,jumper,sizes"
    },
    //检索框提示文字
    searchPlaceholder: {
      type: String,
      default: "请输入检索关键字"
    },
    //数据列表
    data: {
      type: Array,
      default() {
        return [];
      }
    },
    indexColumn: {
      type: Object,
      default() {
        return {
          show: true,
          width: 60
        }
      }

    },
    operateColumn: {
      type: Object,
      default() {
        return {
          show: true,
          width: 100
        }
      }
    },
    checkboxColumn: {
      type: Object,
      default() {
        return {
          show: true
        }
      }
    },
    //列表显示方式，列表、卡片、概览列表
    showType: {
      type: Array,
      default() {
        return ['list']
      }
    },
    //查询条件配置
    searchInfo: {
      type: Array,
      default() {
        return []
      }
    },
    slots: {
      type: Array,
      default() {
        return [{
          name: "default",
          scope: "scope",
          controls: [],
        }];
      }
    },
    browseScope: {
      type: Object,
      default() {
        return {
          "scope": "2",
          "unit": []
        };
      }
    },
    //挂载后立即请求数据
    mountedRequestData: {
      type: Boolean,
      default: true
    },
    //以文本形式代理加载单元格内的组件
    tableTextProxy: {
      type: Boolean,
      default: true
    },
    defaultOrder: {
      type: Array,
      default() {
        return [];
      }
    },
    customerOrder: {
      type: Array,
      default() {
        return [];
      }
    },
    superSearchInfo: {
      type: Array,
      default() {
        return [];
      }
    },
    superSearchDefaultRowsNumber: {
      type: Number,
      default: 2
    },
    superSearchButtonTrigger: {
      type: Boolean,
      default() {
        return false
      }
    },
    showSuperSearchFilter: {
      type: Boolean,
      default: true
    }

  },
  data() {
    return {
      currentPage: 1,
      pageCount: 0,
      isReDrawHander: false,
      searchStr: "",
      removeTable: false,
      loading: false,
      pSize: this.pageSize,
      isLoadDataMark: false,
      // 多选选中值
      multipleSelection: [],
      //高级检索高度
      superSearchHeight: 0,
      superValue: null
    };
  },
  computed: {
    contentWrapHeight() {
      if (this.fit) {
        return 'calc(100% - ' + this.tableMarginTop + 'px - 42px)';
      }
      return 'auto';
    },
    tableMarginTop() {
      return this.superSearchHeight > 0 ? this.superSearchHeight + 5 : 0
    },
    tableHeight() {
      let fit = this.fit;
      if (fit) {
        return '100%';
      }
      return 'auto';
    },
    panelHeight() {
      let fit = this.fit;
      if (this.isDesign) {
        return "100%";
      } else {
        return fit ? "100%" : "auto";
      }
    },
    getCss() {
      return [
        this.$getClass(),
        this.fit ? 'fit' : '',
        this.showPagination ? 'has-pagination' : ''
      ]
    },
    checkedNumber() {
      if (this.$refs.table) {
        return this.$refs.table.selection.length
      }
      else {
        return 0;
      }
    }
  },
  created() {
    createDesignDefaultColumns.call(this);
    this.refreshHeader();
  },
  mounted() {
    if (!this.isDesign) {
      let pSize = _.find(this.limits, (s) => s == this.pageSize);
      if (!pSize) {
        this.$emit("update:pageSize", this.limits[0]);
        if (this.mountedRequestData) {
          this.$nextTick(() => {
            this.loadData();
          })
        }
      }
      else {
        if (this.mountedRequestData) {
          this.loadData();
        }
      }
    }
  },
  updated() {
    this.doLayout();
  },
  beforeDestroy() { },
  destroyed() { },
  methods: {
    reset() {
      if (this.$refs.table) {
        this.$refs.table.clearSelection();
      }
      this.currentPage = 1;
      this.pageCount = 0;
    },
    //更新索引
    indexMethod(index) {
      return (this.currentPage - 1) * this.pageSize + index + 1;
    },
    //获取列头中各个单元格最大的单元格
    getLastColumns(column) {
      let arr = [];
      this.deep(c => {
        if (!c.children || !c.children.length) {
          c.isLast = true;
          arr.push(c);
        } else {
          c.columnId = "";
          c.isLast = false;
        }
      }, column);
      return arr;
    },
    deep(callback, column) {
      var fn = function (columns, level) {
        let columnIndex = 0;
        _.each(columns, (c, i) => {
          callback && callback(c, i, columnIndex, level);
          fn(c.children, level + 1);
          columnIndex++;
        });
      };
      let cols = column ? column : this.columns;
      cols = dsf.type(cols) !== "array" ? [cols] : cols;
      fn(cols, 0);
    },
    //检索框查询
    search() {
      if (this.isDesign) {
        return;
      }
      this.reloadData(1);
    },
    headerDragend() {
      //更新一下列表的布局，否则宽高会出问题
      this.doLayout();
    },
    resize() {
      this.doLayout();
    },
    pageSizeChange(evt) {
      this.$emit("update:pageSize", evt);
    },
    pageIndexChange(evt) {
      this.currentPage = evt;
      this.loadData();
    },
    //更新表头和slot
    refreshHeader() {
      let arr = this.getLastColumns();
      let useSlots = {};
      _.each(arr, (c, i) => {
        let columnId = `${tableColumnSlotName}-${i}`;
        let slot = _.find(this.slots, s => s.name == columnId);
        if (!slot) {
          let newSlot = (slot = {
            name: columnId,
            controls: [
              //此处默认追加一层组件,el-table貌似没有行数据加载过程中的控制事件，所以需要控制单元格中的子元素只能通过追加的一层组件完成
              dsf.designer.createComponent(DsfDataGridCellContent, { "slot-name": "default" })
            ]
          });
          this.slots.push(newSlot);
          slot = newSlot;
        } else {
          slot.name = columnId;
        }
        c.columnId = columnId;
        slot.scope = 'scope';
        slot.repeat = true;
        slot.textProxy = this.tableTextProxy;
        slot.dataKey = "row";
        useSlots[slot.name] = 1; //push(slot.name);
      });
      _.eachRight(this.slots, s => {
        if (s.name.indexOf(tableColumnSlotName + "-") == 0) {
          if (!useSlots[s.name]) {
            dsf.array.remove(this.slots, s);
          }
        }
      });
    },
    addColumn(column, index, parentColumn) {
      let defaultColumn = {
        label: "",
        columnId: "",
        level: 0,
        isLast: false,
        hide: false,
        headerAlign: "center",
        align: "center",
        resizable: false,
        width: "auto",
        children: []
      };
      let col = dsf.mix({}, defaultColumn, column);
      if (parentColumn) {
        if (dsf.isUnDef(index)) {
          parentColumn.children.push(col);
          col.level = parentColumn.level + 1;
        } else {
          parentColumn.children.splice(index, 0, col);
        }
      } else {
        dsf.isUnDef(index) ? this.columns.push(col) : this.columns.splice(index, 0, col);
      }
      return col;
    },
    doLayout() {
      if (this.$refs.table) {
        this.$refs.table.doLayout();
      }
    },
    getRowKey(row) {
      return row._id;
    },
    getQueryInfo() {
      let orderArr = [];
      let queryData = {
        "searchValue": this.searchStr.split(/\s+/g).join(" "),
      };
      if (this.searchInfo) {
        _.each(this.searchInfo, (item) => {
          if (item.type == "1") {
            queryData[item.code] = item.valueExpress;
          }
        });
      }
      if (this.superValue) {
        _.each(this.superValue, (item, i) => {
          queryData[i] = item
        });
      }
      if (this.defaultOrder) {
        let customOrderArr = _.map(this.customerOrder, (order) => {
          return order.sortType ? {
            "code": order.field,
            "sort": order.sortType
          } : undefined;
        });
        customOrderArr = _.filter(customOrderArr, (order) => order);
        let defaultOrderArr = _.map(this.defaultOrder, (order) => {
          let index = _.findIndex(customOrderArr, (f) => f.code == f.field);
          if (index < 0) {
            return {
              "code": order.field,
              "sort": order.sortType
            }
          }

        });
        orderArr = [...customOrderArr, ...defaultOrderArr];
      }
      return {
        order: orderArr,
        query: queryData
      }
    },
    async loadData() {
      if (this.$vm) {
        this.loading = true;
        let data = [];
        let queryInfo = this.getQueryInfo();
        let params = {
          "pageNum": this.currentPage,
          "pageSize": (!this.showPagination) ? 100 : this.pageSize,
          'query': JSON.stringify(queryInfo.query),
          "order": JSON.stringify(queryInfo.order),
          "namespace": this.$vm.nameSpace,
          "pageName": this.$vm.pageName
        };
        let params2 = {
          "pageNum": this.currentPage,
          "pageSize": (!this.showPagination) ? 100 : this.pageSize,
          'query': queryInfo.query,
          "namespace": this.$vm.nameSpace,
          "pageName": this.$vm.pageName
        }
        let url = "/meta/list/data";
        dsf.mix(params, this.queryString)
        this.$http.get(url, params)
          .done((response) => {
            if (response.state != 20000) {
              throw response;
            }
            else {
              data = response.data;
              let url2 = "/meta/list/count";
              dsf.mix(params2, this.queryString)
              return this.$http.get(url2, params);
            }
          })
          .done((response) => {
            this.pageCount = response.data.count;
            this.data.splice(0, this.data.length);
            this.data.push(...data);
          })
          .error((ex) => {
            if (dsf.type(ex) == "string" || ex.message) {
              let msg = ex.message || ex;
              dsf.layer.message(msg, false);
            }
          })
          .always(() => {
            window.setTimeout(() => {
              this.doLayout();
              this.loading = false;
            });
          });
      }

    },
    //刷新数据，不变更页面等信息只重新当前页面的查询数据
    refreshData() {
      this.loadData();
    },
    reloadData(resetPageIndex) {
      if (resetPageIndex) {
        if (this.currentPage != resetPageIndex) {
          this.currentPage = resetPageIndex;
        }
        else {
          this.loadData();
        }
      }
      else {
        this.loadData();
      }
    },
    updateControlPropsInTemplate(ctrlProps, scope) {
      if (!this.isDesign && scope && ctrlProps.isFormControl) {
        ctrlProps.simple = true;
        ctrlProps.readOnly = true;

      }
      return ctrlProps;
    },
    getCurrentMetaData() {
      let childMetaData = [];
      dsf.designer.recursionLayoutTree(this, null, null, child => {
        if (child != this) {
          let designOptions = child.$options.design || {};
          let metadataNode = designOptions.metadata || {};
          let result = metadataNode.create && metadataNode.create.call(child, root);
          if (result && result.metadata) {
            childMetaData.push(result);
            let isDeep = dsf.isDef(metadataNode.isDeep) ? metadataNode.isDeep : true;
            if (!isDeep) {
              return false;
            }
          }
        }
      });

    },
    getSelectedRows() {
      if (this.$refs.table) {
        return this.$refs.table.selection
      }
      else {
        return []
      }
    },
    // elTable 多选监听
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    // 多选时：row 当前行数据 selected 是否选中
    toggleSelect(row, selected) {
      if (this.$refs.table && row) {
        if (!selected) {
          for (let i = 0; i < this.multipleSelection.length; i++) {
            if (v._id == row || v._id == row._id) {
              this.$refs.table.toggleRowSelection(this.multipleSelection[i], false);
              break;
            }
          }
        } else {
          for (let i = 0; i < this.data.length; i++) {
            if (v._id == row || v._id == row._id) {
              this.$refs.table.toggleRowSelection(this.data[i], true);
              break;
            }
          }
        }
      }
    },
    // 多选时：清空用户的选择
    clearSelect() {
      if (this.$refs.table) {
        this.$refs.table.clearSelection();
      }
    },
    //行双击触发
    rowDblClickHandler(row, column, event) {
      let tr = event.currentTarget;
      let buttonbarEl = $(tr).find("td:last div[ctrl_type='dsf.buttonbar']");
      if (buttonbarEl.length > 0) {
        let buttonbar = buttonbarEl.get(0).__vue__;
        let dblClickButtons = _.filter(buttonbar.buttons, (btn) => {
          return btn.isDblClick
        });
        if (dblClickButtons.length > 0) {
          buttonbar.triggerButtonClick(dblClickButtons[0])
        }
      }
    },
    getColumnSort(column) {
      if (this.customerOrder) {
        let result = _.filter(this.customerOrder, (col) => {
          if (column.columnId == col.column) {
            return true;
          }
        });
        if (result.length > 0) {
          return 'custom';
        }
      }
      return false;
    },
    getHeaderClass({ row, column, rowIndex, columnIndex }) {
      let classList = []
      if (column.columnKey) {
        let result = _.filter(this.customerOrder, (order) => order.column == column.columnKey)
        if (result.length > 0) {
          let order = result[0];
          if (order.sortType) {
            let sort = order.sortType + "ending";
            column.order = sort
            classList.push(sort);
          }
          else {
            column.order = null;
          }
        }
      }
      return classList.join(" ");
    },
    tableSort({ column, prop, order }) {
      let result = _.filter(this.customerOrder, (colOrder) => {
        return colOrder.column == column.columnKey;
      });
      if (result.length > 0) {
        let columnOrder = result[0];
        let sort = order ? order.replace("ending", "") : null;
        columnOrder.sortType = sort;
        this.loadData();
      }
    },
    getReturnValue() {
      return this.getSelectedRows();
    }
  },
  watch: {
    columns: {
      handler() {
        this.refreshHeader();
        this.isReDrawHander = true;
        this.$nextTick(() => {
          this.isReDrawHander = false;
        });
      },
      deep: true
    },
    tableTextProxy(v) {
      if (this.isDesign) {
        this.refreshHeader();
        this.isReDrawHander = true;
        this.$nextTick(() => {
          this.isReDrawHander = false;
        });
      }

    },
    value: {
      handler(v) {
        for (let i = 0; i < v.length; i++) {
          !("$editing" in v[i]) && this.$set(v[i], "$editing", false);
          !("$hash" in v[i]) && this.$set(v[i], "$hash", dsf.uuid());
          if ("$order" in v[i]) {
            this.$set(v[i], "$order", i);
          } else {
            v[i]["$order"] = i;
          }
          v[i].$index = i;
        }
      }
    },
    currentPage(v) {
      this.loadData();
    },
    pageSize(v) {
      this.loadData();
    }
  },
  design: {
    isMask: false,
    fit: true,
    layout: {
      exclude: ["value"]
    },
    metadata: {
      isDeep: false,
      create(root) {
        let childMetaData = [];
        dsf.designer.recursionLayoutTree(this, null, null, child => {
          if (child != this) {
            let designOptions = child.$options.design || {};
            let metadataNode = designOptions.metadata || {};
            let result = metadataNode.create && metadataNode.create.call(child, root);
            if (result && result.metadata) {
              childMetaData.push(result);
              let isDeep = dsf.isDef(metadataNode.isDeep) ? metadataNode.isDeep : true;
              if (!isDeep) {
                return false;
              }
            }
          }
        });

        let query = {};
        let searchBox = [], attachSearch = [];
        for (let i = 0; i < this.searchInfo.length; i++) {
          let s = this.searchInfo[i];
          let item = null;
          if (s.type == "0") {
            item = {
              "code": s.code,
              "operator": s.operation,
              "valueKey": "searchValue"
            }
            searchBox.push(item);
          } else {
            item = {
              "code": s.code,
              "operator": s.operation,
              "valueKey": s.code
            }
            attachSearch.push(item);
            s.metadata_fullcode = item.code;
          }
        }
        //添加将检索框条件
        let seachBoxitem = { "logic": "or", items: [] };
        seachBoxitem.items.push(...searchBox);

        //添加默认附加条件
        let attachSearchItem = { "logic": "and", items: [] };
        attachSearchItem.items.push(...attachSearch);

        let searchitems = { "logic": "and", items: [] };
        searchitems.items.push(seachBoxitem);
        searchitems.items.push(attachSearchItem);
        // seachBoxitem.items.length > 0 && searchitems.items.push(seachBoxitem);
        // attachSearchItem.items.length > 0 && searchitems.items.push(attachSearchItem);
        let superSearchItem = { "logic": "and", items: [] }
        _.each(this.superSearchInfo, (el) => {
          let code = null;
          let operator = "";
          let valueKey = null
          if (el.searchType == 'enum') {
            let useFields = [];
            if (el?.children?.length > 0) {
              useFields = _.map(el.children, (f) => f.join == 1);
              useFields = useFields.slice(0, 1);
            }
            else {
              useFields = [el];
            }
            if (useFields.length == 1) {
              code = useFields[0].field
              operator = useFields[0].operator;
              valueKey = useFields[0].field;
              if (operator == "in" || operator == "eq") {
                operator = "in"
              }
              let item = {
                code: code,
                operator: (operator == "in" || operator == "eq") ? "in" : operator,
                valueKey: code
              }
              superSearchItem.items.push(item);
            }
          }
          else if (el.searchType == 'text') {
            code = el.code;
            operator = el.operator;
            valueKey = el.code;
            superSearchItem.items.push({
              code: code,
              operator: operator,
              valueKey: valueKey
            });
          }
          else if (el.searchType == 'datetime') {
            let useFields = [];
            if (el?.children?.length > 0) {
              useFields = _.map(el.children, (f) => f.join == 1);
              useFields = useFields.slice(0, 1);
            }
            else {
              useFields = [el];
            }
            if (useFields.length == 1) {
              superSearchItem.items.push({
                code: useFields[0].field,
                operator: useFields[0].operator,
                valueKey: useFields[0].field
              });
            }
          }
        });
        searchitems.items.push(superSearchItem);
        query = searchitems;
        let metadata = {
          query: query,
          browseScope: this.browseScope,
          _self: this
        }
        return [metadata, ...childMetaData];
      }
    }
  }
});
function createDesignDefaultColumns() {
  if (!this.isDesign) {
    return;
  }
  if (this.columns.length == 0) {
    let size = 4;
    for (let i = 0; i < size; i++) {
      this.addColumn({
        label: `列${i}`,
        level: 0
      });
    }
  }
}
</script>