<template>
  <div class="ds-page-home">
    <div v-if="hasHeader" class="ds-page-home-header" :class="{'common-top':isTrue}">
      <slot name="header"></slot>
    </div>
    <div :style="sectionStyle" class="ds-page-home-section" :class="{'common-section':isTrue}">
      <div v-if="hasAside && menuAction[0].children" class="ds-page-home-aside">
        <slot name="aside"></slot>
      </div>
      <div :style="asideStyle" class="ds-page-home-main">
        <slot></slot>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex';

export default {
  name: 'DsfHome',
  provide() {
    return {
      root: this
    };
  },
  props: {
    menuList: {
      type: Array,
      default() {
        return []
      }
    },
    isTrue: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      hasHeader: false,
      hasAside: false,
      menuAction: [],
      menuIndex: [],
      menuKey: []
    }
  },
  computed: {
    ...mapState([
      'route'
    ]),
    sectionStyle() {
      const opt = {};
      if (!this.hasHeader) {
        opt.top = '0'
      }
      return opt;
    },
    asideStyle() {
      const opt = {};
      if (!this.hasAside || !this.menuAction[0].children) {
        opt.left = '0'
      }
      return opt;
    }
  },
  watch: {
    'route' () {
      this.initMenu();
    },
    'menuList' () {
      this.initMenu();
    }
  },
  created() {
    if (this.$slots.header) {
      this.hasHeader = true;
    }
    if (this.$slots.aside) {
      this.hasAside = true;
    }
    this.initMenu();
  },
  methods: {
    initMenu () {
      let menuAction = [];
      let menuIndex = [];
      let menuKey = [];
      this.filterMenu(menuAction, menuIndex, menuKey, this.menuList, this.route, 0);
      this.menuAction = menuAction;
      this.menuIndex = menuIndex;
      this.menuKey = menuKey;
      // 如果检测到当前为空路由，即route.fullPath等于'/'
      // 则跳转到第一个菜单去
      if (menuKey.join('') === '' && this.route.fullPath === '/' && this.menuList.length) {
        if (this.menuList[0].menu_url) {
          location.replace(this.menuList[0].menu_url);
        } else if (this.menuList[0].children && this.menuList[0].children.length) {
          location.replace(this.menuList[0].children[0].menu_url);
        }
      }
    },
    /**
     * 判断当前选中的菜单
     * @param menuAction
     * @param menuIndex
     * @param menuKey
     * @param menuList  菜单列表
     * @param route     route
     * @param n         递归层级
     */
    filterMenu(menuAction, menuIndex, menuKey, menuList, route, n) {
      menuAction[n] = {};
      menuIndex[n] = -1;
      menuKey[n] = '';
      for (let i = 0; i < menuList.length; i++) {
        const item = menuList[i];
        if (item.menu_url && this.urlsIsSame(item, route)) {
          menuAction[n] = item;
          menuIndex[n] = i;
          menuKey[n] = item.menu_id;
          if (!item.children || !item.children.length) {
            break;
          }
        }
        if (item.children && item.children.length) {
          this.filterMenu(menuAction, menuIndex, menuKey, item.children, route, n + 1);
          if (menuIndex[n + 1] !== -1) {
            menuAction[n] = item;
            menuIndex[n] = i;
            menuKey[n] = item.menu_id;
            break;
          }
        }
      }
    },
    /**
     * url2是否符合url1
     * path相同，url1的query在url2中都能找到
     * @param item
     * @param route
     */
    urlsIsSame (item, route) {
      let obj = item.__url_obj || null;
      let url = item.menu_url;
      if (!obj && url) {
        if (url.indexOf('/#') === 0) {
          url = url.substr(2);
        } else if (url.indexOf('#') === 0) {
          url = url.substr(1);
        }
        obj = item.__url_obj = dsf.url.analyzing(url);
      }
      if (!obj || obj.path !== route.path) {
        return false;
      }
      let rq = null, iq = null;
      for (let k in obj.query) {
        rq = route.query[k];
        iq = obj.query[k];
        if (!rq) return false;
        else if (dsf.type(iq) !== 'array' && rq !== iq) return false;
        else if (dsf.type(iq) === 'array' && iq.indexOf(rq) === -1) return false;
      }
      return true;
    }
  }
}
</script>