import "_platform/dsf/utils";
import "../dsf_config";

//注入平台系统元数据
import "_platform/dsf/metadata";
import "../metadata";

//注入平台基础组件
import bhc from "_platform/dsf/pc/bhc";
//注入平台全局过滤器
import filters from "_platform/dsf/filters";
//用于加载配置完成的页面,预览页面
import output from '_platform/dsf/output'

import components from './components';

// home页面组件
import Home from '_platform/dsf/pc/home';

import api from '../api';

// 引入项目样式
// import "../assets/styles/pc/dsfStyle.scss";
import '../assets/styles/pc/onething.scss';

//此处引入要使用的组件
import App from "./App.vue";
import store from "../store";
import * as types from '../store/mutation-types';
import router from "./router";
import Vue from "vue";


//引入表单
import "../mixins/form";

Vue.use(bhc);
Vue.use(filters);
Vue.use(output);
Vue.use(Home);
Vue.use(components);
Vue.prototype.dsf = dsf;
Vue.prototype.$http = dsf.http;
Vue.config.productionTip = false;

//注入WEBAPI
Object.defineProperties(Vue.prototype, {
  $webAPI: {
    get: function() {
      if (!this.$$webapi) {
        this.$$webapi = {};
        for (let k in api) {
          this.$$webapi[k] = api[k].bind(this)
        }
      }
      return this.$$webapi;
    }
  }
});

router.beforeEach((to, from, next) => {
  // 非登录页，且需要登录，跳转到登录页面
  if (to.path !== '/pc/login' && to.meta.needLogin && dsf.cookies.get('isLogin') !== '1') {
    location.replace('./page.html#/pc/login?from=' + encodeURIComponent(location.href));
    return;
  }
  //通过路由的title修改页面标题
  document.title = to.meta.title || dsf.config.defaultTitle || "";
  //优先判断路由是否存在
  next();
});

router.afterEach((to) => {
  // 存路由信息，供导航菜单组件使用
  store.commit(types.UPDATE_ROUTE, {
    route: to
  });
});

Vue.config.devtools = true;

new Vue({
  store,
  router,
  created() {
    //监听window窗体变化后，递归组件执行组件的resize函数
    dsf.resizeDetector();
    //修复IE前进后退路由变化没效果的问题
    if ("-ms-scroll-limit" in document.documentElement.style && "-ms-ime-align" in document.documentElement.style) {
      window.addEventListener("hashchange", () => {
        var currentPath = window.location.hash.slice(1);
        if (this.$router.path !== currentPath) {
          this.$router.replace(currentPath);
        }
      }, false);
    }
  },
  render: h => h(App)
}).$mount("#app");