<template>
  <el-dialog ref="dialog" class="ds-dialog" v-bind.sync="$attrs" @closed="closeHander" v-on="$listeners">
    <template v-slot:title>
      <slot name="title"></slot>
    </template>
    <template v-slot:default>
      <slot></slot>
    </template>
    <template v-slot:footer>
      <slot name="footer"></slot>
    </template>
  </el-dialog>
</template>
<script>
/**
 * @class DsfDialog
 * @description 对话框
 * @example
 * <DsfDialog>
 *  <template slot="title">我是标题</template>
 *  <template>我是主体部分</template>
 *  <template slot="footer">我是底部</template>
 * </DsfDialog>
 */
export default {
  /**
   * @memberof DsfDialog
   * @name Slots
   * @property {*} title 标题部分
   * @property {*} default 主体内容部分
   * @property {*} footer 底部部分
   */
  name: "DsfDialog",
  inheritAttrs: false,
  provide(){
    return {
      $dialog:this
    }
  },
  data() {
    return {};
  },
  mounted() { 
  },
  methods: {
    closeHander() { },
  },
};
</script>
