<template>
  <div class="full-view">
    <dsf-home v-if="isHome" :menu-list="menuList" :is-true="isTrue">
      <dsf-one-thing-home-header v-if="isTrue" slot="header" />
      <dsf-home-header v-else slot="header" :dropdown-config="dropdownConfig" @dropdown-click="dropdownClick" />
      <dsf-home-aside slot="aside" />
      <router-view class="full-view" fit style="min-height:100%"></router-view>
    </dsf-home>
    <router-view v-else class="full-view page-head-font-fixed"></router-view>
  </div>
</template>

<script>
import DsfOneThingHomeHeader from '../components/DsfOneThingHomeHeader'
export default {
  name: 'Home',
  components: {
    DsfOneThingHomeHeader
  },
  props: {
    isHome: {
      type: Boolean,
      default: false
    },
    isTrue: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      // 用户头像下的菜单
      dropdownConfig: [
        // { name: '个人信息', key: 'personInfo', url: '/dsfa/teas/jwgl/jsgrxx/views/jsgrxx.html?id=@%5Bsession(%27user_id%27)%5D' },
        // { name: '我的收藏', key: 'myCollect' },
        // { name: '我的红色慕课', key: 'myClass' },
        // { name: '我的积分', key: 'myIntegral' },
        // { name: '修改密码', key: 'alterPassword' },
        // { name: '切换为学员', key: 'changeRole' },
        { name: '退出', key: 'loginOut', handler: this.loginOut }
      ],
      menuList: []
    }
  },
  created() {
    if (this.isHome) {
      this.getMenuTree();
    }
  },
  methods: {
    // 用户头像下的菜单点击
    dropdownClick(item) {
      console.log(item.name);
      if (item.url) {
        if (item.url.startsWith("#")) {
          let url = item.url.substr(1);
          this.$router.push(url);
        }
      } else if (item.handler) {
        item.handler(item);
      }
    },
    // 请求菜单数据
    getMenuTree() {
      const homeMenu = dsf.storage.session.get('homeMenu');
      if (homeMenu) {
        this.menuList = homeMenu;
      } else {
        this.$http.get(
          window.$$config.home.menu.src
        ).done(({ success, data }) => {
          if (success) {
            this.menuList = data;
            dsf.storage.session.set('homeMenu', data);
          }
        }).error(({ message }) => {
          dsf.layer.message(message, false);
        });
      }
    },
    // 退出登录
    loginOut() {
      this.$webAPI.loginOut()
        .then(({ success }) => {
          dsf.cookies.set('authorization_token', '');
          dsf.cookies.set('user_name', '');
          dsf.cookies.set('user_id', '');
          dsf.cookies.set('isLogin', '');
          dsf.storage.session.clear();
          location.replace('./page.html#/pc/login');
        })
        .catch((ex) => {
          dsf.layer.message(ex, false);
        })
    }
  }
};
</script>
